import Vue from 'vue';
import api from '@/api/api';


const GETADDRESSLIST = 'GETADDRESSLIST';
const SETDEFAULTADDRESS = 'SETDEFAULTADDRESS';
const DELADDRESS = 'DELADDRESS';

const CHECKADDRESS = 'CHECKADDRESS';


export default {
	state: {
		addressList: [],//地址列表
		page:0,
		pageSize: 10,
		totalPage: 0,
		checkedOrderAddress:'',//选择的订单页地址
	},
	actions: {
		getAddressList({ commit ,state}){
			var parma = {
				pageNo: state.page,//		number	
				pageSize: state.pageSize
			}
			api.get('api/appclient/v1/user/address/order/list',{},function(res){
				var res = res.body;
				commit('GETADDRESSLIST',res.data)
			})
		},
		getAddressListManager({ commit ,state}){
			var parma = {
				pageNo: state.page,//		number	
				pageSize: state.pageSize
			}
			api.get('api/appclient/v1/user/address/order/list',{},function(res){
				var res = res.body;
				commit('GETADDRESSLIST',res.data)
			})
		},
		setDefaultAddress({ commit },option){
			api.get('/setDefaultAddress',{id: option.id},function(res){
				var res = res.body;
				commit('SETDEFAULTADDRESS',option)
			})
		},
		delAddress({ commit },option){
			api.get('/delAddress',{id: option.id},function(res){
				var res = res.body;
				commit('DELADDRESS',option)
			})
		},
		getEditAddress({ commit },id){
			return new Promise(function(resolve,reject){
				api.get('/addressEditGet',{id:id},function(res){
					var res = res.body;
					if(res.meta.code == '00000'){
						resolve(res);
					}
				})
			})
			
		},
		checkAddress( { commit,state }, item){
			state.checkedOrderAddress = item;
		}

	},
	mutations: {
		GETADDRESSLIST(state,data){
			state.addressList = data.itemList;
			state.page = data.page;
			state.totalPage = data.totalPage;
		},
		SETDEFAULTADDRESS(state, option){
			state.addressList.map((item)=> item.isDefault = 0);
			state.addressList[option.index].isDefault  = 1;
		},
		DELADDRESS(state,option){
			state.addressList.splice(option.index,1);
		}
	}
}


