<template>
  <div id="deliveryArea">

    <div class="deliveryArea-header">
      <img src="../../../assets/icon/close2.png" class="deliveryArea-header-close" @click="close"/>
      <!--<router-link to="/register" slot="right">-->
        <!--<span class="loginEntrance-header-register">注册</span>-->
      <!--</router-link>-->
      <div class="deliveryArea-header-title">查看配送区域</div>
      <div></div>

    </div>
    <hr style="" class="deliveryArea-hr"/>


    <div class="deliveryArea-search"></div>
    <div class="deliveryArea-content">
      <div id="map-container"></div>
    </div>


  </div>
</template>

<script>
  //  import { MessageBox } from 'mint-ui'
  import bus from '../../../js/eventBus'
  export default {
    name: 'deliveryArea',
    mounted () {
      this.init()
    },
    destroyed () {
    },
    computed: {
    },
    methods: {
      close () {
//        console.log(document.getElementsByClassName('mint-tabbar')[0].style.display)
        this.$router.push('/')
      },
      async init(){
        // 百度地图API功能
         var map = new BMap.Map("map-container");    // 创建Map实例
         map.centerAndZoom(new BMap.Point(116.404, 39.915), 11);  // 初始化地图,设置中心点坐标和地图级别
         map.addControl(new BMap.MapTypeControl());   //添加地图类型控件
         map.setCurrentCity("北京");          // 设置地图显示的城市 此项是必须设置的
         map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
      }
    }
  }
</script>



<style lang="scss">
  $baseFontSize: 750px;
  $defaultColor: #191919;
  @function pxToRem($px) {
    @return $px / $baseFontSize * 10rem;
  }
  $defaultSize: pxToRem(24px);

  #deliveryArea {
    position: fixed;
    top: 0;
    left:0;
    right:0;
    bottom: 0;
  }

  .deliveryArea-header {
    position: fixed;
    height: pxToRem(100px);

    top: 0px;
    left: pxToRem(30px);
    right: pxToRem(30px);

    display: flex;
    justify-content: space-between;
    align-items: center;

  }
  .deliveryArea-header-close {
    width: pxToRem(20px);
  }

  .deliveryArea-header-title {
    font-size: pxToRem(24px);
    color: #191919;
  }

  .deliveryArea-search {
    position: fixed;
    height: pxToRem(134px);
    left:0;
    right:0;
    top:pxToRem(100px);;
  }

  .deliveryArea-content {
    position: absolute;
    top: pxToRem(234px);
    left:0;
    right:0;
    bottom:0px;
  }

  #map-container {
    position: absolute;
    top: 0;
    left:0;
    right:0;
    bottom:0;
    /*z-index: 100;*/
    /*margin-top: pxToRem(234px);*/
    height: 100%;
    /*!*height: pxToRem(970px);*!*/
    /*background-color: #26a2ff;*/
  }

  .deliveryArea-hr {
    position: absolute;
    top: pxToRem(100px);
    left:0;
    right:0;
    height: 1px;
    background-color: #d8d8d8;
  }
</style>


