<template>
	<div class="detailsHeader">
		<span><span class="icon-closed" onclick="history.back();"></span></span>
		<div class="details-title">
			幸福西饼--{{title}}
		</div>
		<span class="icon-cart-group">
			<span class="icon-cart" @click="goShopCart">{{counter}}</span>
		</span>
	</div>
</template>
<script>
	import '@/utils/getShopCartCount'

	import { mapState } from 'vuex'
	export default {
		data(){
			return {

			}
		},
		computed: mapState({
		    counter: state => state.shopCart.counter,
		    title: state => state.proDetails.title
  		}),
  		methods: {
  			goShopCart(){
				this.$router.push({ name:'ShoppingCart',query: { 'isProCart': true }});
  			}
  		},
  		mounted(){
  			this.$store.dispatch('getCartNum');
  		}

	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.detailsHeader {
		padding: 0 pxToRem(30px);
		display: flex;
		justify-content: space-between;
		height: pxToRem(100px);
		line-height: pxToRem(100px);
		background-color: #fff;
		opacity: .9;

		.vt-middle {
			display: inline-block;
			padding: 0 10%;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellpsis;
			width: 1px;
		}
	}
	.details-title {
		padding: 0 10%;
		overflow: hidden;
    	white-space: nowrap;
    	text-overflow: ellipsis;
	}
	.icon-cart-group {
		display: flex;
		align-items: center;
		.icon-cart {
			text-align: center;
		    line-height: pxToRem(40px);
		    color: #666;
		    font-size: pxToRem(20px);
		}
	}
</style>


