<template>
	
<div class="default-panel">
	<normalBar title="收货地址"></normalBar>
	<div class="empty-sec" v-if="empty">
		当前暂无收货地址
	</div>
	
	<div class="address-list">
		<mt-cell-swipe
			v-for=" (item,index) in addressList"
		  	:right="[
		  		{
			      	content: '设为默认',
			      	style: { background: '#D4B675', color: '#fff' },
			      	handler: () => {setDefaultAddress(item.userId,index)}
	    		},
			    {
			      content: '删除',
			      style: { background: '#D8331D', color: '#fff' },
			      handler: () => {	delAddress(item.userId,index)  }
	    		}
  			]"
  			
  		>
			<div class="address-sec"
				@click="item.checkStatus==1&&checkedAddress(item)"
				:class="{disabled: item.checkStatus == 0}"
			>
				<div class="address-content">
					<p class="title" v-if="item.isDefault">默认地址</p>
					<p class="big-size">{{item.recipientName}}<span class="last">收</span></p>
					<p>{{item.address}}</p>
					<p class="phone">{{item.recipientMobile}}</p>
				</div>
				<router-link :to="'/addressEdit/edit/'+item.userId"><span class="icon-edite" ></span></router-link>
			</div>
  		</mt-cell-swipe>
	</div>



	<router-link to="/addressEdit/add">
		<div class="empty-footer-sec">
			<span>+ </span> 新建地址
		</div>
	</router-link>

</div>

</template>

<script>
	import Vue from 'vue';
	import { CellSwipe } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	Vue.component(CellSwipe.name, CellSwipe);

	import normalBar from '@/components/common/navBar/normalBar'

	import '@/utils/addressListGet'
	import '@/utils/addressDefault'
	import '@/utils/addressDel'

	import { mapState} from 'vuex'
 
	export default {
		data(){
			return {
				empty: false,
				id: '123'
			}
		},
		computed: {
			...mapState({
				addressList: state => state.addressState.addressList
			})
		},
		components: {
			normalBar
		},
		methods: {
			checkedAddress(item){
				console.log(item);
				if( !this.$route.query.isOrderAddress) return false;
				let self = this;
				this.$store.dispatch('checkAddress',item).then(function(){
					self.$router.push({
						name: 'firmOrder'
					})
				});
			},
			setDefaultAddress(id,index){
				let parma = {
					id: id,
					index: index
				}
				this.$store.dispatch('setDefaultAddress',parma)

			},
			delAddress(id,index){
				let self = this;
				
				let parma = {
					id: id,
					index: index
				}
				MessageBox.confirm('','',{
					message: '是否确认删除地址',
				}).then(function(action){
					self.$store.dispatch('delAddress',parma)
				},function(action){
					alert(action);
				})
			
			},
			getAddressList(){
				this.$store.dispatch('getAddressListManager');
			}
		},
		mounted(){
			this.getAddressList();
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.empty-sec {
		padding: pxToRem(100px) 0;
		color: #666666;
		text-align: center;
	}
	.empty-footer-sec {
		padding: pxToRem(40px) 0;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		background-color: #fff;
		font-size: pxToRem(28px);
		color: #D8331D;
		text-align: center;
		border-top: 1px solid #ccc;
	}

	.address-list {
		padding-left: pxToRem(50px);
		.mint-cell-wrapper {
			padding: 0;
			.mint-cell-title {
				display: none;
			}
		}

		.mint-cell-swipe {
			.mint-cell-value {
				width: 100%;
			}
		}

		.address-sec {
			padding-left: 0;
			border: none;
			border-bottom: 1px solid #ccc;

			&.disabled {
				// background-color: rgba(0,0,0,.2);
				color: #ccc;
				.big-size {
					color: #ccc;
				}
			}
		}
		.mint-cell-swipe-buttongroup {
			display: flex;
		}

		.mint-cell-swipe-button {
			width: pxToRem(110px);
			vertical-align: top;
			color: #fff;
			line-height: 1.2em;
			text-align: center;
			display: inline-flex;
			align-items: center;
			justify-content: center;
		}
	}

</style>


