<template>
	
	<div class="flex-bar">
		<span class="icon-goback" onClick= "history.back();"></span>
		<div class="flex-title">
			<span class="vt-middle">{{mainTitle}}</span>
		</div>
	</div>
</template>

<script>
	export default	 {
		props: ['title'],
		computed: {
			mainTitle(){
				return this.title
			}
		}
	}
</script>

<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);



	.flex-bar {
		padding: 0 pxToRem(30px);
		background-color: #fff;
		display: flex;
		justify-content: space-around;
		height: pxToRem(100px);
		line-height: pxToRem(100px);
		border-bottom: 1px solid #eee;
	}

	.flex-title {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		background-color: rgba(255,255,255,0.90);
	}
</style>


