<template>
    <div id = "chooseCity">

      <Picker v-if="showPiker" @picker-event="pickerEvent" :dataSlots="dataSlots" >

      </Picker>

      <div id="search" v-if="Object.keys(cityData).length > 0">
        <img src="../../../assets/icon/search.jpg" id="search-img"/>
        <div id="search-text-view">
          <div id="search-text" style="color:#BDBDBD" @click="searchClick">请输入城市进行搜索</div>
          <!--<input type="text" id="search-text"-->
                 <!--value="请输入城市进行搜索"-->
                 <!--onfocus="if(value=='请输入城市进行搜索'){value=''};style='color:#191919'"-->
                 <!--onblur="if(value==''){value='请输入城市进行搜索'};style='color:#BDBDBD'"-->
                 <!--@input="searchTextChange"/>-->
        </div>
        <!--<img  src="../../../assets/icon/close.png" id="search-close" />-->

        <div class="sparator search-sparator"></div>
      </div>

      <SearchCity v-if="showSearchCity">

      </SearchCity>
      <mt-index-list :show-indicator="true" v-if="Object.keys(cityData).length > 0">

        <div id="header">

          <div id="header-localPosition">

            <img id="header-localPosition-img" src="../../../assets/icon/location.png"/>
            <div class="header-lp-city" @click="localPositionEvent">
              <div class="header-localPosition-lable header-localPosition-lable1">{{localCity}}</div>
              <div class="header-localPosition-lable header-localPosition-lable2">当前城市</div>
            </div>

          </div>
          <div id="header-hotCity">

            <div id="header-hotCity-center">
              <div class="header-hotCity-lable1">
                热门城市
              </div>
              <div class="header-hotCity-hotBtnView">
                <div v-for="city in hotData" class="header-hotCity-hotBtn" @click="chooseCityEvent(city)">{{city.cityName}}</div>
              </div>
            </div>

          </div>
        </div>

        <mt-index-section v-bind:index="cityName"  v-for="(cityName,index) in cityNameData" id="section">

          <!--<div id="section-header">{{cityName}}</div>-->

          <div v-for="(city,cityIndex) in cityData[cityName]" class="parentCell">

            <div class="wrapper">
              <div @click="selectedCity(city.name, city, false)"
                   v-bind:id="city.name" class="cell">
                  {{city.name}}
              </div>
              <div v-if="(cityIndex < (cityData[cityName].length-1)) || isShowChildCity[city.name]" class="sparator"></div>
            </div>

            <div class="wrapper" v-if="isShowChildCity[city.name]" v-for="(childCity,childCityIndex) in city.childCitys">
              <div @click="selectedCity(childCity.name, childCity, true)" class="cell cell-child">
                  {{childCity.name}}
              </div>
              <div v-if="(cityIndex < cityData[cityName].length-1) || childCityIndex < city.childCitys.length-1" class="sparator sparator-child"></div>
            </div>

          </div>
        </mt-index-section>
      </mt-index-list>

    </div>
</template>

<script>
    import mapHelper from '@/js/mapHelper'
    import api from '@/api/common/api.common'
    import bus from '@/js/eventBus'
    import SearchCity from './searchCity'
    import Picker from '../Picker/Picker.vue'
    export default {
      name: 'chooseCity',
      components: {
        SearchCity,
        Picker
      },
      data () {
        return {
          dataSlots: [],
          cityData: {
            //通过api获取数据后，重新封装成这个数据
//            'C': [{'name': '长沙', 'id': '123', childCitys: [{'name': '芙蓉区', 'id': '1245'}, {'name': '长沙市', 'id': '1246'}]},
//                  {'name': '长沙2', 'id': '124'},
//                  {'name': '长沙3', 'id': '125', childCitys: [{'name': '芙蓉区', 'id': '1245'}, {'name': '长沙市', 'id': '1246'}]}],
//            'S': [{'name': '深圳', 'id': '1'},
//                  {'name': '深圳2', 'id': '2'},
//                  {'name': '深圳3', 'id': '3'}]
          },
          isShowChildCity: {
            '长沙': false,
            '长沙3': false
          },
          localCity: '正在定位',
          address: {},
          hotData: [],
          showSearch: false,
          selectedChildCitys: [],
          showSearchCity: false,
          showPiker: false,
          cityNameData: []
        }
      },
      mounted () {
        var self = this
        bus.$on('closeSearchCity',function (data) {
          console.log(data)
          self.showSearchCity = data
        })

        // 定位
        mapHelper.getCurrentLocation(function (address) {
          self.address = address
          self.localCity = address.city
        })
        // 获取热门城市
        api.getHotCity({}, function (successRespond) {
          self.hotData = successRespond.body.data
          var city = {}
          city.cityName = '肇庆市'
          city.hasThirdCity = true
          self.hotData.push(city)
        })
        //获取城市列表
        api.getAllCity({}, function (successRespond) {

          var cityData = {}
          var cityNameData = []
          var childCity = []

          try {
            for (var x in successRespond.body.data) {

              var city = successRespond.body.data[x]
              var firstName = city.cityNamePingyin.slice(0,1).toLocaleUpperCase()

              if (cityData.hasOwnProperty(firstName)) {
                var cityInfo = self.getNewCity(city)
                if (city.isCity == 1) {
                  if (city.cityName === city.regionalismName) {
                    cityData[firstName].push(cityInfo)
                  } else { //将三级城市先存储下来，防止先读取到三级城市找不到市
                    childCity.push(cityInfo)
                  }
                }
              } else {
                cityData[firstName] = []
                var cityInfo = self.getNewCity(city)

                if (city.isCity == 1) {
                  if (city.cityName === city.regionalismName) {
                    cityData[firstName].push(cityInfo)

                  } else { //将三级城市先存储下来，防止先读取到三级城市找不到市
                    childCity.push(cityInfo)
                  }
                }
                cityNameData.push(firstName)
              }

            }

            self.cityData = cityData
            cityNameData.sort()
            self.cityNameData = cityNameData

            var isShowChildCity = {}
            for (var x in childCity) {
              var cCity = childCity[x]
              var firstName = cCity.cityNameSpell.slice(0,1).toLocaleUpperCase()
              var citys = self.cityData[firstName]

              for (var y in citys) {
                var parentCity = citys[y]
                if (parentCity.name === cCity.name) {
                  isShowChildCity[cCity.name] = false
                  cCity.name = cCity.regionalismName
                  if (parentCity.hasOwnProperty('childCitys')) {
                    parentCity.childCitys.push(cCity)
                  } else {
                    var val = parentCity
                    parentCity.childCitys = []
                    parentCity.childCitys.push(val)
                    parentCity.childCitys.push(cCity)
                  }
                }
              }

            }
          } catch (err) {
            self.utils.alert('获取城市列表失败，请尝试刷新页面')
          }

          self.isShowChildCity = isShowChildCity
          self.$store.dispatch('setCitys' , cityData);
        },function (err) {
          self.utils.alert('获取城市列表失败，请尝试刷新页面')
        })
      },
      methods: {
        localPositionEvent () {
          if (this.localCity === '正在定位') {
            return
          }

          this.localCity = '肇庆市'
          var isAV = false
          for (var x in Object.keys(this.cityData)) {
            var citys = this.cityData[Object.keys(this.cityData)[x]]

            for (var y in citys) {
              var city = citys[y]
              if (city.name === this.localCity) {
                if (city.hasOwnProperty('childCitys') && city.childCitys.length > 0) {
                  var pickerData = this.getChildCityPickerData(city,city.childCitys)
                  this.dataSlots = pickerData
                  this.showPiker = true
                  this.selectedChildCitys = city.childCitys
                  isAV = true
                  return
                } else {
                  this.$store.dispatch('setUserCity' , city)
                  this.$router.push({path: '/'})

                  return
                }
              }
            }
          }

          if (!isAV) {
            this.utils.alert('当前城市暂未开通，敬请期待')
            this.$router.push({path: '/'})
          }
        },
        pickerEvent (eventObj) {
          this.showPiker = false;
          if(eventObj.hasOwnProperty('selectData')) {

            var cityName = eventObj.selectData[1]
            for (var i in this.selectedChildCitys) {
              var city = this.selectedChildCitys[i]
              console.log(city.cityName + cityName)
              if (city.cityName === cityName) {

                this.$store.dispatch('setUserCity' , city)
                this.$router.push({path: '/'})
              }
            }
          }

        },
        searchClick () {
          this.showSearchCity = true
        },
        searchTextChange (val) {
          console.log(val.srcElement.value)
          bus.$emit('sendSearchCity', val.srcElement.value)
        },
        getNewCity (city) {
          //"name": "佛山市"
//          "cityId": 440600,
//            "cityName": "佛山市",
//            "cityNamePingyin": "fo shan shi",
//            "isCity": 1,
//            "provinceId": 440000,
//            "provinceName": "广东省",
//            "regionalismId": 440600,
//            "regionalismName": "佛山市",
//            "regionalismNamePingyin": "fo shan shi"
          var cityInfo = {}
          cityInfo.name = city.cityName.replace(/\s+/g,"")
          cityInfo.cityName = city.cityName.replace(/\s+/g,"")
          cityInfo.id = city.cityId
          cityInfo.cityNameSpell = city.cityNamePingyin
          cityInfo.cityId = city.cityId
          cityInfo.provinceName = city.provinceName.replace(/\s+/g,"")
          cityInfo.regionalismName = city.regionalismName.replace(/\s+/g,"")
          cityInfo.regionalismId = city.regionalismId
          cityInfo.regionalismNameSpell = city.regionalismNamePingyin
          cityInfo.isCity = city.isCity

          return cityInfo
        },
        selectedCity (cityName, city, isChildCity) {
          var isShow = this.isShowChildCity[cityName]
          if (isShow !== undefined && !isChildCity) {
            this.isShowChildCity[cityName] = !isShow
          } else {
//            console.log(city)
//            this.utils.alert('选择了:' + cityName)
            this.$store.dispatch('setUserCity' , city)
            this.$router.push({path: '/'})
          }
        },
        chooseCityEvent (city) {
          if (city.hasThirdCity) {
            var childCitys = this.getCityChild(city)

//            var pickerData = this.getChildCityPickerData(city,childCitys)
            var pickerData = []

            var data1 = {}
            data1.flex = 1
            data1.className = 'slot1'
            data1.values = [city.cityName]
            data1.textAlign = 'center'
            pickerData.push(data1)

            var data2 = {}
            data2.flex = 1
            data2.className = 'slot1'
            var values = []
            for (var i in childCitys) {
              var childCity = childCitys[i]
              values.push(childCity.name)
            }
            data2.values = values
            data2.textAlign = 'center'
            pickerData.push(data2)

            this.dataSlots = pickerData
            this.showPiker = true
            this.selectedChildCitys = childCitys
          } else {
            var newCity = {}
            newCity.name = city.cityName
            newCity.cityId = city.cityId
            newCity.cityName = city.cityName
            newCity.regionalismId = city.cityId
            this.$store.dispatch('setUserCity' , newCity)
            this.$router.push({path: '/'})
          }
        },
        getCityChild (city2) {
          // 查找该城市三级城市列表
          for (var x in Object.keys(this.cityData)) {
            var citys = this.cityData[Object.keys(this.cityData)[x]]

            for (var y in citys) {
              var city = citys[y]
              var cityNameSpell = city.cityNameSpell.replace(/\s+/g, "")
              if (city.name === city2.cityName) {
                return city.childCitys
              }
            }
          }
        },
        getChildCityPickerData (city,childCitys) {
          var pickerData = []

          var data1 = {}
          data1.flex = 1
          data1.className = 'slot1'
          data1.values = [city.cityName]
          data1.textAlign = 'center'
          pickerData.push(data1)

          var data2 = {}
          data2.flex = 1
          data2.className = 'slot1'
          var values = []
          for (var i in childCitys) {
            var childCity = childCitys[i]
            values.push(childCity.name)
          }
          data2.values = values
          data2.textAlign = 'center'
          pickerData.push(data2)

          return pickerData
        }
      }
    }
</script>

<style lang="scss">
  $baseFontSize: 750px;
  $defaultColor: #191919;
  @function pxToRem($px) {
  @return $px / $baseFontSize * 10rem;
  }
  $defaultSize: pxToRem(24px);

  input,button,select,textarea{outline:none;  border:none;}
  textarea{resize:none}

  #search {
    height: pxToRem(100px);
    position: relative;
  }
  #search-img {
    display: inline-block;
    margin-left: pxToRem(60px);
    margin-top: pxToRem(35px);
    height: auto;
    width: pxToRem(30px);
  }
  #search-text-view {
    display: inline-block;
    position: absolute;
    left: pxToRem(124px);
    top: 0px;
    bottom: 0px;

  }
  #search-text {
    position: absolute;
    top: pxToRem(32px);
    width: pxToRem(500px);
  }
  #search-close {
    position: absolute;
    right: pxToRem(74px);
    width: pxToRem(20px);
    height: auto;
    top: pxToRem(40px);
  }

  #header{
    /*height: pxToRem(386px);*/
  }


  #header-localPosition {
    height: pxToRem(80px);
    /*background-color: black;*/
    border-bottom: solid 1px #d8d8d8;
    position:relative;
  }
  #header-localPosition-img {
    width: pxToRem(20px);;
    display: inline;
    position: absolute;
    left: pxToRem(40px);
    top: pxToRem(24px);
  }
  .header-lp-city {
    display: inline;
    position: absolute;
    left: pxToRem(80px);
    top: 0px;
    bottom: 0px;
  }
  .header-localPosition-lable {
    display: inline;
    color: #191919;
    line-height: pxToRem(80px);
    vertical-align: middle;
    font-size: $defaultSize;
  }
  .header-localPosition-lable2 {
    color: #CCCCCC;
    font-size: pxToRem(24px);
    line-height: pxToRem(80px);
  }


  #header-hotCity {
  }
  #header-hotCity-center {
    margin-left: 0px;
    margin-right: 20px;
  }
  .header-hotCity-lable1 {
    /*position:absolute;*/
    left: pxToRem(40px);
    margin-left: pxToRem(40px);
    margin-top: pxToRem(60px);
    font-size: pxToRem(24px);
  }
  .header-hotCity-hotBtn {
    /*flex:1 20%;*/
    width: pxToRem(180px);
    text-align: center;
    margin-left: pxToRem(40px);
    height: pxToRem(60px);
    margin-bottom: pxToRem(20px);
    color: #191919;
    padding-top: pxToRem(12px);
    /*line-height: pxToRem(40px);*/
    border: .5px solid rgba(102,102,102,1);
    font-size: pxToRem(24px);
  }
  .header-hotCity-hotBtnView {
    display: flex;
    flex-flow: row wrap;
    margin-top: pxToRem(20px);
    margin-bottom: pxToRem(40px);
  }

  .cell {
    background-color: white;
    text-align: left;
    height: pxToRem(62px);
    line-height: pxToRem(62px);
    width: 100%;
    vertical-align: middle;
    font-size: pxToRem(24px);;
    padding-left: pxToRem(40px);
  }
  .cell-child {
    padding-left: pxToRem(60px);
  }
  .wrapper {
    height: pxToRem(64px);
    padding-right: 20px;
    position: relative;
  }
  .sparator {
    /*width: 100%;*/
    height: 1px;
    position: absolute;
    bottom: 0px;
    left: pxToRem(18px);
    right: 0px;
    background-color: #d8d8d8;
  }
  .sparator-child {
    left: pxToRem(56px);
  }
  .search-sparator {
    left: pxToRem(0px);
  }
  .mint-indexsection-index{
    height: pxToRem(49px);
    background-color: #eeeeee;
    text-align: left;
    padding-left: pxToRem(40px);
    padding-top: pxToRem(10px);
    color: #bd8d46;
    font-size: pxToRem(24px);
  }
  #section-header {
    height: pxToRem(25px);
    background-color: #eeeeee;
  }
  .mint-indexlist-nav {
    border-left: solid 0px #ddd;
  }


</style>



