<template>
	<span class="operate-group">
        <span class="icon-reduce" @click="reduce"></span>
        <span class="text-counter">{{counts}}</span>
        <span class="icon-plus" @click="plus"></span>
    </span>

</template>

<script>
	export default {
		props:['count','sid','index'],
		data(){
			return {
				counts: 0

			}
		},
		watch:{
			count(val,oldVal){
				this.counts =  val;
			}
		},
		mounted(){
			this.counts =  this.count;
		},
		methods: {
			reduce(){
				if(this.counts >1 ){
					-- this.counts;
					this.$emit('reduce',this.counts,this.sid,this.index);
				}
			},
			plus(){
				++ this.counts;
				this.$emit('plus',this.counts,this.sid,this.index);
			}
		}

	}
</script>


