<template>
    <div class="default-panel prolist-panel" id="proList">
        <proHeaders></proHeaders>
        <div class="pd30 bg-sec">
            <div class="pro-img-swiper">
                <mt-swipe :auto="4000">
                    <mt-swipe-item class="pro-slide">
                        <img  v-lazy="productDetail.detailImg1" alt="">
                    </mt-swipe-item>
                     <mt-swipe-item class="pro-slide">
                        <img  v-lazy="productDetail.detailImg2" alt="">
                    </mt-swipe-item>
                     <mt-swipe-item class="pro-slide">
                        <img  v-lazy="productDetail.detailImg3" alt="">
                    </mt-swipe-item>
                     <mt-swipe-item class="pro-slide">
                        <img  v-lazy="productDetail.detailImg4" alt="">
                    </mt-swipe-item>
                </mt-swipe>
            </div>
            
            <div class="pro-titles" v-cloak>
                <h1>{{currSkuBean.displayName}}</h1>
                <p class="text-red" >¥<span v-if="currSkuBean.price">{{currSkuBean.price | filterPrice}}</span></p>
                <p class="text-grey">门市价:¥<span v-if="currSkuBean.originalPrice">{{currSkuBean.originalPrice | filterPrice }}</span></p>
            </div>
            <!-- 规格 tlp start -->
            <div class="pro-spec">
                <p class="text-grey">选择规格</p>
                <div class="spec-list">
                    <span class="item-spec" :class="{'active': activeSpec == index}" v-for="(item,index) in productSkuList" @click="getCurrSku(index)">
                        {{item.productSkuBeanVo.size}}
                    </span>
                </div>
                <div class="spec-details">
                    <ul class="spec-details-list">
                        <li v-for="item in currSkuSize">{{item.specKey}}：{{item.specValue}}</li>
                    </ul>
                </div>
            </div>
            <!-- 规格 tlp end -->
            <!-- 选择数量 tlp end -->
            <div class="counter-operate">
                <span class="text-grey">选择数量</span>
                <span>
                    <span class="icon-reduce" @click="reduce"></span>
                    <span class="text-counter">{{counter}}</span>
                    <span class="icon-plus" @click="plus"></span>
                </span>
            </div>
            <!-- 选择数量 tlp end -->
            <div class="pro-help">
                <span class="item-help" @click="promiseShow=true">幸福承诺</span><span class="item-help">查看配送区域</span>
            </div>
        </div>
        <div class="pd30 bg-sec">
            <div class="pro-details">
                <h3 class="title">产品详情</h3>
                <div class="pro-details-info">
                    <img v-lazy.container="imgList.value" class="page-lazyload-image" alt="" v-for="imgList in templateData">
                </div>
            </div>
        </div>

        <div class="pro-footer">
            <span class="i-cart" @click="addToCart">加入购物车</span>
            <span class="i-buynow">立即购买</span>
        </div>

        <mt-popup
          v-model="promiseShow"
          position = 'bottom'
          class="promise-popup"
          > 
            <span class="promise-btn" @click="promiseShow=false">我知道了</span>
            <div class="promise-sec">
                <p class="title">幸福承诺</p>
                <p class="promise-line"><span class="i-b">就敢退</span> 实物不对版，退款不退货！</p>
                <p class="promise-line"><span class="i-b">就敢减</span> 约定时间！迟到1分钟减1元！</p>   
                <p class="promise-line"><span class="i-b">就敢送</span> 迟到30分钟免费赠送！</p>
            </div>
        </mt-popup>

    </div>
</template>
<script>
    import Vue from 'vue';
    import { Swipe, SwipeItem } from 'mint-ui';
    import { Popup } from 'mint-ui';
    import { Lazyload } from 'mint-ui';
    Vue.use(Lazyload);
    Vue.component(Swipe.name, Swipe);
    Vue.component(SwipeItem.name, SwipeItem);
    Vue.component(Popup.name, Popup);

    import proHeaders from '@/components/home/productDetail/proDetailHeader'


    // 注册
    Vue.filter('filterPrice', function (value) {
        console.log(value);

        if(!!value){
            return (value/100).toFixed(2);
        }else {
            return ''
        }
    })
    

    import '@/utils/getProDetails'
    import '@/utils/addToCart'

    export default {
        data(){
            return {
                promiseShow : false,
                activeSpec: 0,
                currSkuBean: {},//当前所选规格信息
                currSkuSize: {},//当前所选规格尺寸信息
                templateData: [],//详情
                counter: 1,//数量
            }
        },
        components: {
            proHeaders
        },
        computed: {
            productSkuList(){
                return this.$store.state.proDetails.productSkuDetailList
            },
            productDetail(){
                return this.$store.state.proDetails.productDetail
            }
        },
        watch:{
            productSkuList:function(){
                this.currSkuBean = this.productSkuList[0].productSkuBeanVo;
                this.currSkuSize = this.productSkuList[0].productSkuSizeSpecVoList; 
                this.templateData = this.productDetail.templateData;
            }
        },
        mounted(){
            var self = this;
            self.getProInfo();

        },
        methods: {
            getProInfo(){
                this.$store.dispatch('getDetails').then(function(res){
                    console.log(res);
                });
            },
            getCurrSku(index){
                this.activeSpec = index;
                this.currSkuBean = this.productSkuList[index].productSkuBeanVo;
                this.currSkuSize = this.productSkuList[index].productSkuSizeSpecVoList;
            },
            reduce(){
                if(this.counter <= 1){
                    return false;
                }else {
                    this.counter --;
                }
            },
            plus(){
                this.counter ++;
            },
            addToCart(){
                let proInfo = {
                    num: this.counter,
                    skuId: this.currSkuBean.id
                }
                this.$store.dispatch('addCart',proInfo)
            }

        }

    }
</script>

<style lang="scss">
    $baseFontSize: 750px;
    @function pxToRem($px) {
        @return $px / $baseFontSize * 10rem;
    }
    
    .page-lazyload-image[lazy=loading] {
        width: 100% !important;
        height: 300px;
        background-color: rgba(0,0,0,.3);
    }

    .prolist-panel {
        background-color: #eee;
    }

    .bg-sec {
        background-color: #fff;
    }

    .page-infinite-listitem {
        height: 150px;
        line-height: 150px;
    }

    .pro-img-swiper {
        width: 100%;
        height: pxToRem(750px);
        .pro-slide {
            img {
                max-width: 100%;
                min-height: 100%;
            }
        }
        .mint-swipe-indicator {
            background-color: rgba(255,255,255,1);
            opacity: 0.5;
            transform: scale(.8);
            &.is-active{
                background-color: rgba(255,255,255,1);
                opacity: 1;
                transform: scale(1);
            }
        }
    }

    .pro-titles {
        padding: pxToRem(60px) 0;
        text-align: center;
        border-bottom: 1px solid #ccc;
        h1 {
            font-size: pxToRem(28px);
            font-weight: normal;
            line-height: 1.2em;
        }
        .text-red ,.text-grey {
            margin-top: pxToRem(20px);
            line-height: 1;
        }
        .text-grey {
            font-size: pxToRem(20px);
        }

    }

    .pro-spec {
        padding: pxToRem(60px) pxToRem(10px) 0 pxToRem(10px);

        .spec-list {
            margin-top: pxToRem(20px);
            font-size: 0;
            
            .item-spec {
                display: inline-block;
                width: pxToRem(140px);
                height: pxToRem(60px);
                line-height: pxToRem(60px);
                text-align: center;
                font-size: pxToRem(26px);
                border: 1px solid #666;
                &:not(:last-of-type) {
                    margin-right: pxToRem(36px);
                }
                &.active {
                    background-color: #D8331D;
                    color: #fff;
                    border-color: #D8331D;
                }
            }
        }

        .spec-details {
            margin-top: pxToRem(40px);
            padding: pxToRem(20px) pxToRem(16px) pxToRem(60px) pxToRem(16px);
            color: #666666;
            border-top: 1px solid #EEEEEE;

            .spec-details-list {
                font-size: 0;
                li {
                    padding: pxToRem(10px);
                    display: inline-block;
                    font-size: pxToRem(20px);
                    vertical-align: top;
                    width: 50%;
                }
            }
        }
    }

    .counter-operate {
        padding: pxToRem(40px) pxToRem(10px);
        display: flex;
        justify-content: space-between;
        line-height: pxToRem(40px);
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #ccc;

        .text-counter {
            margin: 0 pxToRem(40px);
        }

    }
    
    .pro-help {
        padding: pxToRem(40px) pxToRem(10px); 
        display: flex;
        justify-content: space-between;

        .item-help {
            border-bottom: 1px solid #191919;
        }
    }
    
    .pro-details {
        margin-top: pxToRem(60px);
        padding-top: pxToRem(80px);
        text-align: center;

        .title {
            font-size: pxToRem(24px);
            font-weight: normal;
        }

        .pro-details-info {
            margin-top: pxToRem(58px);
            padding-bottom: pxToRem(80px);

            img {
                width: 100%;
                max-width: 100%;
            }
        }
    }

    .pro-footer {
        position: fixed;
        display: flex;
        width: 100%;
        bottom: 0;
        left: 0;
        text-align: center;
        .i-cart,.i-buynow {
            flex: 1;
            line-height: pxToRem(110px);
            color: #fff;
            font-size: pxToRem(32px);
        }
        .i-cart {
            background-color: #D4B675;
        }
        .i-buynow {
            background-color: #D8331D;
        }
    }

    .promise-popup {
        width: 100%;
        text-align: center;
        background-color: #EEEEEE;

        .promise-sec {
            padding: pxToRem(40px) 0;
            .promise-line {
                padding: pxToRem(16px) 0;
                line-height: 1;
            }

            .i-b {
                font-weight: bold;
            }
        }

        .title {
            padding: pxToRem(16px) 0;
            font-size: pxToRem(28px);
        }

        .promise-btn {
            padding: pxToRem(36px) 0;
            display: inline-block;
            width: 100%;
            background-color: #fff;

        }

    }
</style>


