<template>
	<div class="right-nav-content">
		<ul class="nav-list">
			<li v-for="(navItem,index) in navList[0]">
				<div class="nav-item" @click="goNav(navItem.id)">{{navItem.name}}</div>
				<ul class="sub-nav-list">
					<li class="sub-nav-item" v-for="subNavItem in navList[navItem.type]" @click="goNav(subNavItem.id,subNavItem.parentId)">{{subNavItem.name}}</li>
				</ul>
			</li>
		</ul>
	</div>
</template>
<script>
	import Vue from 'vue';
	import { mapState } from 'vuex'

	import '@/utils/getClassifyNav'
	import '@/utils/getProList'

	export default {
		data(){
			return {
			}
		},
		computed: mapState({
			navList: state => state.homeState.navList,
		}),
		mounted(){
			this.getNavList();
		},
		methods:{
			getNavList(){
				this.$store.dispatch('getNavList');
			},
			goNav(id,parentId){
				this.$store.dispatch('navShowToggle');
				this.$store.dispatch('getCategory',{id,parentId});
				this.$store.dispatch('getClassifyProList');
			}
		}
	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.right-nav-wap {
		padding: pxToRem(100px) pxToRem(30px) pxToRem(110px) 0;
		display: inline-block;
		width: pxToRem(558px);
		height: 100%;
		background: #fff;
		top: 50%;
	    right: 0;
	    bottom: auto;
	    left: auto;
	    -webkit-transform: translate3d(0,-50%,0);
	    transform: translate3d(0,-50%,0);
	    opacity: .96;
	}

	.right-nav-content {
		padding-top: pxToRem(120px);
		width: 100%;
		height: 100%;
		background-color: #eeeeee;

		.nav-list {
			width: 100%;
			font-size: pxToRem(28px);

			li {
				.nav-item {
					padding: pxToRem(18px) 0 pxToRem(18px) pxToRem(140px);
					font-weight: bold;
					&:active {
						background-color: #fff;
					}
				}
			}
			.sub-nav-list {
				margin-bottom: pxToRem(45px);
				font-size: pxToRem(24px);
				li {
					padding: pxToRem(18px) 0 pxToRem(18px)  pxToRem(164px);
					&:active {
						background-color: #fff;
					}
				}
			}
		}
	}
	
</style>


