<template>
	
<div class="default-bg">
	<normalBar title="选择付款方式"></normalBar>
	
	<ul class="pay-list">
		<li>
			<div class="pay-item">
				<span class="checkbox-group">
					<input type="radio" class="input-checkbox" name="payTypes">
					<span class="icon-checkbox"></span>
				</span>
				<div>
					<span class="icon-wx"></span>
					微信安全支付
				</div>
			</div>
		</li>
		<li>
			<div class="pay-item">
				<span class="checkbox-group">
					<input type="radio" class="input-checkbox" name="payTypes">
					<span class="icon-checkbox"></span>
				</span>
				<div>
					<span class="icon-zfb"></span>
					支付宝支付
				</div>
			</div>
		</li>
	</ul>

	<button type="button" class="btn-sure" @click="payOrder">确认</button>
</div>

</template>

<script>
	import normalBar from '@/components/common/navBar/normalBar'

	export default {
		components: {
			normalBar
		},
		methods: {
			payOrder(){
				this.$router.push({name: 'paySuccess'})
			}
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	
	.pay-list {
		margin-top: pxToRem(20px);
		padding-left: pxToRem(50px);
		background-color: #fff;

		li {
			padding: pxToRem(40px) 0;

			.pay-item {
				display: flex;
				align-items: center;

				.checkbox-group {
					width: pxToRem(36px);
					height: pxToRem(38px);
				}
			}
			
			&:not(:last-of-type){
				border-bottom: 1px solid #BDBDBD;
			}
		}
	}

	.btn-sure {
		margin: pxToRem(100px) auto;
		display: block;
		width: pxToRem(656px);
		height: pxToRem(100px);
		color: #fff;
		background-color: #D8331D;
		border: none;
		font-size: pxToRem(28px);
	}


</style>


