<template>
	<div class="flex-bar">
		<span class="icon-goback"  v-show="isProCart" onClick= "history.back();"></span>
		<div class="flex-title">
			<span class="vt-middle">购物车</span>
		</div>
		<span class="flex-right">
			<span class="edite" @click="editeToggle">{{isEdite ? '完成':'编辑'}}</span>
		</span>
	</div>
</template>

<script>
	import Vue from 'vue';
	import { mapState } from 'vuex'
	
	export default {
		name: 'aressBar',
		props: ['isProCart','isEdite'],
		data() {
			return {

			}
		},
		computed: mapState({
			navShows: state => state.homeState.navBtnShow,
		}),
		components: {
		},
		methods:{
			navShowClick(){
				this.$emit('navShowClick');
			},
			editeToggle(){
				this.$emit('editeToggle');
			}
			
		}
	}
</script>

<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}

	.flex-bar {
		padding: 0 pxToRem(30px);
		display: flex;
		justify-content: space-around;
		height: pxToRem(100px);
		line-height: pxToRem(100px);
		border-bottom: 1px solid #eee;
	}

	.flex-title {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		background-color: rgba(255,255,255,0.90);
	}
	.edite {
		display: inline-block;
		width: pxToRem(60px);
		color: #666;
	}

	
</style>


