<template>
  <div id="userAccountSet">

    <mt-header title="幸福西饼-账号设置" style="height: 50px">
      <router-link to="/mine" slot="left">
        <mt-button>
          <img src="../../../assets/icon/back.png" height="auto" width="8px" slot="icon" style="margin-left: 12px">
        </mt-button>
      </router-link>
    </mt-header>
    <hr style="height: 1px;background-color: #d8d8d8;"/>

    <router-link to="/userAccountSet/info">
      <div class="userAccountSet-cell">
        <div class="userAccountSet-cell-leftTitle">个人信息</div>
        <div class="userAccountSet-cell-rightView">
          <img v-bind:src="userIcon" id="userAccountSet-userIcon"/>
          <img src="../../../assets/icon/continue.png" style="width: 7px;height: auto;vertical-align: 60%"/>
        </div>
      </div>
      <hr class="sparator"/>
    </router-link>


    <div class="userAccountSet-cell">
      <div class="userAccountSet-cell-leftTitle">账号绑定</div>

      <div class="userAccountSet-cell-rightView" v-if="isBindMobile === true">
        {{mobile}}
      </div>

      <router-link to="/userAccountSet/bindMobilePhone" v-if="isBindMobile === false">
        <div class="userAccountSet-cell-rightView" >

          <span id="userAccountSet-unbind">未绑定</span>
          <img src="../../../assets/icon/continue.png" style="width: 7px;height: auto"/>
        </div>
      </router-link>


    </div>
    <hr class="sparator"/>

    <router-link :to="{ path: '/resetPassword', query: { type: 'resetpassowrd' }}" >
      <div class="userAccountSet-cell" v-if="isBindMobile === true">

        <!--<router-link to="{ path: '/resetPassword', query: { type: 'resetpassowrd' }}" >-->

          <div class="userAccountSet-cell-leftTitle">修改/设置密码</div>
          <div class="userAccountSet-cell-rightView">
            <img src="../../../assets/icon/continue.png" style="width: 7px;height: auto"/>
          </div>


      </div>
    </router-link>
    <hr class="sparator" v-if="isBindMobile === true"/>
  </div>


</template>

<script>

  export default {
    computed: {
      isBindMobile () {
        return this.$store.state.user.mobilePhone.length > 10
      },
      mobile () {
        return this.$store.state.user.mobilePhone
      },
      userIcon () {
        return this.$store.state.user.avatar
      }
    },
    data () {
      return {

      }
    }
  }
</script>

<style lang="scss">
  $baseFontSize: 750px;
  $defaultColor: #191919;
  @function pxToRem($px) {
    @return $px / $baseFontSize * 10rem;
  }
  $defaultSize: pxToRem(24px);

  .userAccountSet-cell {
    font-size: pxToRem(24px);
    margin-left: pxToRem(50px);
    margin-right: 0px;
    height: pxToRem(122px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    /*line-height: pxToRem(135px);*/
  }

  #userAccountSet-unbind {
    font-size: pxToRem(24px);
    margin-right: pxToRem(20px);
    color: #191919;
    margin-top: 0px;
    margin-bottom: 0px;
    /*background-color: #26a2ff;*/
    vertical-align: text-top;

  }
  .userAccountSet-cell-leftTitle {
    color: #BDBDBD;
    display: inline-block;
    height: pxToRem(122px);
    line-height: pxToRem(122px);

  }

  .userAccountSet-cell-rightView {
    margin-right: pxToRem(52px);
    font-size: pxToRem(24px);
    text-align: right;
    height: pxToRem(122px);
    align-items: center;
    display: flex;

  }

  .sparator {
    height: 1px;
    background-color: #d8d8d8;
    margin-left: pxToRem(50px);

  }

  #userAccountSet-userIcon {
    width: pxToRem(70px);
    height: pxToRem(70px);
    background: #EEEEEE;
    -moz-border-radius: pxToRem(35px);
    -webkit-border-radius: pxToRem(35px);
    border-radius: pxToRem(35px);

    border-width: 0px;
    margin-right: pxToRem(20px);
    border-color: #000 #000 #000 #000;
  }
</style>


