<template>

  	<div class="shopcart-wrap">
		<cartHeader :isProCart="isProCart" 
			:isEdite = "isEdite"
			v-on:editeToggle = "editeToggle"
		></cartHeader>

		<div class="default-panel" v-if="shopCartList.length == 0">
			<p class="empty">当前购物车暂无商品</p>
		</div>
		<div class="default-panel" v-else>
			
			<!-- 商品列表 start-->
			<div v-for="(item,index) in shopCartList" class="grey-bg-sec">

			<mt-cell-swipe  
				class="cart-swiper"
			  	:right="[
				    {
				      content: '删除',
				      style: { 
				      	background: 'red',
				      	color: '#fff'
				      	},
				      handler: () => { delCartItem(item.sid,index)}
				    }
			  	]">
			  	<div class="pro-table-sec">
					<div class="pro-table">
						<div class="pro-table-left">
							<span class="checkbox-group">
								<input type="checkbox" class="input-checkbox"  
									:value="item.sid" 
									v-model="proChecked"
									@change="checkedToggle($event,item.sid,item.goodsType,index)"
									>
								<span class="icon-checkbox"></span>
							</span>
						</div>
						<div class="pro-table-right">
							<div class="media">
								<div class="media-left">
									<img :src="item.picture" alt="">
								</div>
								<div class="media-content">
									<p class="m-title">
										<span>{{item.displayName}}</span>
										<span class="m-small" v-if="item.goodsType == 1">{{item.size}}</span>
									</p>
									<span class="m-subTitle" v-if="item.goodsType == 1"><span class="label">赠送</span>1刀＋10盘＋10叉＋10湿巾</span>
									<div class="m-operate">
										<div class="m-operate-cont">
											<span class="price text-red">¥{{item.price | filterPrice}}</span>
											<counter
												:index = 'index'
												:sid = 'item.sid'
												:count = 'item.num' 
												v-on:reduce = 'updateNum'
												v-on:plus = 'updateNum'
											></counter>
										</div>
								
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="pro-table-footer" v-if="item.goodsType == 1">
						<span class="birth-brand">生日牌</span>
						<span :class="{'text-grey': !item.extra.chocoBoard }" @click="birthPopup(index)">{{item.extra.chocoBoard ? item.extra.chocoBoard :'如需要可点击添加'}}<span class="icon-arrow-right"></span></span>
					</div>
					<div class="pro-table-footer part-table-footer" v-if="item.goodsType == 2 && item.displayName=='数字蜡烛'">
						<span class="birth-brand">数字备注</span>
						<span class="text-grey"><input type="text" class="part-mark" placeholder="请输入0-9任意数字，多个请用逗号隔开" v-model="candle" @change="updateExtra(item.sid,index,0)"/></span>
					</div>
				</div>
			</mt-cell-swipe>
			</div>
			<!-- 商品列表 end-->

			<!-- 配件列表 hasCake为true时显示start-->
			<productPart v-if="hasCake"></productPart> 
			<!-- 配件列表 end-->
		</div>
		
		<div class="car-bottom-sec" :class="{'bottom': isProCart}">
			<div class="car-bottom-content">
				<div class="car-bottom-left">
					<div class="checkbox-group-content">
						<span class="checkbox-group">
							<input type="checkbox" class="input-checkbox" value="true" v-model="checkedAll" @change="checkedAllToggle">
							<span class="icon-checkbox"></span>
						</span>全选
					</div>
					<span v-show="!isEdite">
						合计：
						<span class="text-red">¥{{finalOrderPrice | filterPrice}}</span>
						<span class="text-grey">含运费</span>
					</span>
				</div>
				<span class="checkout-btn" :class="{'disabled':checkedNum==0}" @click="deleteChooseItem" v-if="isEdite">
					删除所选<span v-if="checkedNum>0">（{{checkedNum}}）</span>
				</span>
				<span class="checkout-btn" :class="{'disabled':checkedNum==0}" @click="ckeckout" v-else>
					下单结算<span v-if="checkedNum>0">（{{checkedNum}}）</span>
				</span>
			</div>
		</div>
		
		<mt-popup
			class="birth-wrap"
		  v-model="popupVisible"
		  position="right">
		  <birthBrand
		  	v-on:birthShow = 'birthPopupToggle'
		  	v-on:saveBirthText = 'getBirthText'
		  ></birthBrand>	
		</mt-popup>

		<mainNavBar v-show='!isProCart'></mainNavBar>
	</div>
</template>

<script>
	import Vue from 'vue';
	
	import { MessageBox } from 'mint-ui'
	import { CellSwipe } from 'mint-ui';
  	import { Toast } from 'mint-ui';
	Vue.component(CellSwipe.name, CellSwipe);
	import { Popup } from 'mint-ui';
	Vue.component(Popup.name, Popup);


	import cartHeader from './shoppingCartHeader'
	import birthBrand from './birthBrand'
	import counter from '../counter'
	import productPart from './productPart'

	import mainNavBar from '@/components/common/mainNavBar'

	import '@/utils/getShopCart'
	import '@/utils/greetingCard'
	import '@/utils/deleteCartItem'
	import '@/utils/updateCartItem'
	import '@/utils/doGoodsExtra'
	

	import { mapState,mapGetters } from 'vuex'

	export default {
		data(){
			return {
				isEdite: false,//不否为编辑状态
				accesListShow: false,//显示配件
				popupVisible: false,
				proChecked: [],//选择的项
				checkedAll: [],
				isProCart: false,//是否是从产品详情页进入
				checkedNum: 0,//已选产品数
				currBirthIndex: 0,//当前编辑要选择生日牌
				candle: null,
				hasCake: true
			}
		},
		computed: {
			...mapState({
				shopCartList: state => state.shopCart.shopCartList,//商品列表信息
				finalOrderPrice: state => state.shopCart.finalOrderPrice,//最终订单价
				freight: state => state.shopCart.freight,//运费
				totalPrice: state => state.shopCart.totalPrice,//商品总价
				candleModel: function(state){
					for(let i = 0; i < state.shopCart.shopCartList.length ; i++){
						if(state.shopCart.shopCartList[i].goodsType == 2
						 && state.shopCart.shopCartList[i].displayName == "数字蜡烛"){
							return state.shopCart.shopCartList[i].extra.candleModel;
						}else {
							return '';
						}
					}
					
				}
			})
		},
		components: {
			cartHeader,
			birthBrand,
			counter,
			mainNavBar,
			productPart
		},
		watch:{
			proChecked: function(){
				this.checkedNum = this.proChecked.length;
				if(this.proChecked.length < this.shopCartList.length){
					this.checkedAll = [];
				}
			},
			candleModel: function(){
				this.candle = this.candleModel;
			},
			shopCartList: function(val){
				var temp = [];
				val.map(function(item){
					temp.push(item.goodsType);
				})
				this.hasCake = temp.some((index)=> index==1);
			}
		},
		mounted(){
			
			console.log(this.$route.query);
			this.isProCart = this.$route.query.isProCart ? this.$route.query.isProCart: false;
			
			this.$store.dispatch('getCartInfo');
		},
		methods: {
			checkedToggle(event,sid,goodstype,index){
				console.log(event.target.checked+':'+sid+':'+goodstype+':'+index);
				let skuPro = {
					checked: event.target.checked,
					sidList: [sid]
				}
				this.$store.dispatch('updateCartItem',skuPro)
			},
			checkedAllToggle(){//全选
				var self  = this;
				
				this.$nextTick(function(){
					let shopCartList = self.shopCartList;
					let len = shopCartList.length;
					if(self.checkedAll.length>0){
						self.proChecked = [];
						for(let i = 0 ; i < len; i++){
							self.proChecked.push(shopCartList[i].sid);
						}
					}else {
						self.proChecked=[];
					}
					console.log(this.proChecked);

					if(this.isEdite) return false; //编辑状态时不记得选择

					let skuPro = {
						checked: this.checkedAll[0] ? true : false,
						sidList: this.proChecked,
					}
					this.$store.dispatch('updateCartItem',skuPro)
				})
				
			},
			birthPopup(index){//
				this.currBirthIndex = index;
				this.birthPopupToggle();
			},
			birthPopupToggle(){ //是否显示生日牌
				this.popupVisible = !this.popupVisible;
			},
			getBirthText(text){
				let self = this;
				let parma = {
					extra:{ 
					    chocoBoard: text
					},
					sid: self.shopCartList[self.currBirthIndex].sid
				}
				this.$store.dispatch('updateExtra',parma).then(function(){
					self.shopCartList[self.currBirthIndex].extra.chocoBoard = text;
				});

			},
			updateExtra(sid,index,type){//修改type 0数字蜡烛备注,type 1 生日牌
				let parma = {
						extra:{ 
						    candleModel: this.candle,
						},
						sid: sid,
				}
				this.$store.dispatch('updateExtra',parma)


				this.shopCartList[index].extra.candleModel = this.candle;
				console.log(sid);
			},
			//数量修改
			updateNum(count,sid,index){
				let self  = this;
				
				var skuPro = {
					num: count,
					sid: [sid],
				}
				this.$store.dispatch('updateCartItem',skuPro).then(function(){
					self.shopCartList[index].num = count;
				});

			},
			plusCount(count){
				this.textCount = count;
			},
			delCartItem(skuId,index){//删除商品
				var self  = this;
				MessageBox.confirm('','',{
					title: '',
					message: '是否确认删除',
					showCancelButton: true,
				}).then(function(action){
					console.log(action);
					self.$store.dispatch('delCartItem',{skuId: [skuId],index:index}).then(function(){
						let proChecked = self.proChecked;
						self.proChecked = proChecked.filter((item)=>{
							return item != skuId
						})
					})
				
				},function(action){
					console.log(action);

				})
			},
			ckeckout(){//下单结算
				let self  = this;
				let shopCartList = this.shopCartList;
				let proChecked = this.proChecked;

				if(self.checkedNum <= 0) return false;

				let checkedItem = [];//当前选中商品
				for(let i = 0 ; i < shopCartList.length ; i++){
					for(let j = 0; j < proChecked.length ; j++){
						if(shopCartList[i].sid ==  proChecked[j]){
							checkedItem.push(shopCartList[i])
						}
					}
				}

				console.log(checkedItem);

				for(let i = 0 ; i < checkedItem.length ; i++){
					let items = checkedItem[i];
					if(items.goodsType == 2 && items.displayName == '数字蜡烛'){
						if(self.vailNumMark(items.extra.candleModel) == ''){
							MessageBox.prompt('sss','ssss',{
								title: '',
								message: '您还未填写数字蜡烛备注，请输入',
								inputPlaceholder: '请输入0-9任意数字，多个请用逗号隔开',
								showCancelButton: true,
								showInput: true
							}).then(function({value,action}){
								alert(value);
								self.candle = value && value;
								self.shopCartList[i].extra.candleModel = value && value;
							},function(value){
								console.log(value);
							})

							return false;
							break;
						}else if(items.goodsType == 2 && self.vailNumMark(items.extra.candleModel) !== items.num){
							MessageBox.prompt('','',{
								title: '',
								message: '数字个数需要与购买数量一致，请重新输入',
								inputPlaceholder: '请输入0-9任意数字，多个请用逗号隔开',
								showCancelButton: true,
								showInput: true
							}).then(function({value,action}){
								console.log(value);
								self.candle = value && value;
								self.shopCartList[i].extra.candleModel = value && value;
							},function(value){
								console.log(value);
							});
							return false;
							break;
						}	
					}
				}

				let chooseGdsTypes = [];
				
				for(let j = 0; j < checkedItem.length ; j++) {
					chooseGdsTypes.push(checkedItem[j].goodsType);
				}

				if(chooseGdsTypes.some((index)=> index==1)){
					this.$router.push({
						name: 'firmOrder'
					})
				}else {
					Toast('配件只可以和幸福蛋糕一起配送，请重新选择');

				}

			},
			deleteChooseItem(){
				var self = this;
				if(this.checkedNum <=0 ) return false;
				this.$store.dispatch('delCartItem',{skuId: this.proChecked}).then(function(){
					self.proChecked = [];
				})
			},
			vailNumMark(val){
				if(val == ''){
					return ''
				}else {
					let marksArr = val.split(',');
					return marksArr.length;
				}
			},
			editeToggle(){//编辑完成按钮
				this.isEdite = !this.isEdite;
				this.proChecked = [];
			}
		}
	}
</script>


<style lang="scss">	
$defaultColor: #191919;
$baseFontSize: 750px;

@function pxToRem($px) {
	@return $px / $baseFontSize * 10rem;
}

$defaultSize: pxToRem(24px);

.shopcart-wrap {
	padding-bottom: pxToRem(150px);
}

.grey-bg-sec {
	padding-bottom: pxToRem(20px);
	background-color: #eee;
}

.part-border {
	border: 1px solid #BDBDBD;
}

.part-table-footer {
	.part-mark {
		width: 100%;
    	text-align: right;
		border: none;
		// border: 1px solid #ccc;;

	}
}

.cart-swiper {
	&:not(:first-of-type){
		margin-top: pxToRem(20px);
	}
	.pro-table-sec {
		width: 100%;
		background-color: #fff;
	}

	.mint-cell-wrapper {
		padding: 0;

		.mint-cell-title {
			display: none;
		}
		.mint-cell-value {
			width: 100%;
		}
	}

	.mint-cell-swipe-button {
		padding: 0;
		width: pxToRem(150px);
		display: flex;
		align-items: center;
		test-align: cener;
		justify-content: center;
		font-size: pxToRem(24px);
	}
}

.car-bottom-sec {
	position: fixed;
	bottom: pxToRem(110px);
	left: 0;
	width: 100%;
	font-size: $defaultSize;
	border-top: 1px solid #eee;
	background-color: #fff;
	&.bottom {
		bottom: 0;
	}
	
	.car-bottom-content {
		display: flex;

		.car-bottom-left {
			padding: 0 pxToRem(20px) 0 pxToRem(40px);
			flex: 1;
			display: inline-flex;
			align-items: center;
			justify-content: space-between;

			.text-gray {
				margin-left: pxToRem(20px);
			}

		}
		.checkout-btn {
			width: pxToRem(230px);
			height: pxToRem(115px);
			line-height: pxToRem(115px);
			text-align: center;
			color: #fff;
			font-size: pxToRem(32px);
			background-color: #D8331D;
			&.disabled {
				background-color: #CCCCCC;
			}
		}
	}
}

.checkbox-group-content {
	display: flex;
    align-items: center;
}

.birth-wrap {
	width: 100%;
    height: 100%;
}

.empty{
	margin-top: pxToRem(102px);
	text-align: center;
	color: #666666;
}

</style>


