import Mock from 'mockJs'
var Random = Mock.Random;

Random.extend({
    constellation: function(date) {
        var constellations = ['约1磅', '约3磅', '约2磅']
        return this.pick(constellations)
    }
})
Random.extend({
    goodsType: function(date) {
        var constellations = [1]
        return this.pick(constellations)
    }
})

Random.extend({
    classifyName: function(date) {
        var constellations = ['幸福蛋糕','幸福下午茶','幸福手信','幸福赠品']
        return this.pick(constellations)
    }
})


export default  Mock.mock('/api/appclient/v1/cart/getCartInfo',{
    "data": {
        "cartItemVo": [
        {
            "classifyName": "@classifyName",
            "displayName": "数字蜡烛",
            "favorite": false,
            "goodsType": '2',
            "num": 3,
            "picture": Random.image( 180 ),
            "pid": Random.integer( 10000, 50000),
            "price": Random.natural(1000,80000),
            "sid": 114289,
            "size": '@constellation',
            "extra":{
                "candleModel": '1,3',
            }  
        },{
            "classifyName": "@classifyName",
            "displayName": Random.csentence(10,20),
            "favorite": false,
            "goodsType": '@goodsType',
            "num":  Random.integer( 1,10),
            "picture": Random.image( 180 ),
            "pid": Random.integer( 10000, 50000),
            "price": Random.natural(1000,80000),
            "sid": Random.natural(50000,80000),
            "size": '@constellation',
            "extra":{
                "chocoBoard": '生日快乐哟'
            }  
        }
        ],
        "finalOrderPrice": 82325,
        "freight": 51688,
        "totalGoodsPrice": 81242,
        "itemGsTypes": [1,2]
    },
    "meta": {
        "code": "00000",
        "msg": "ok",
        "success": true
    }

});


