<template>

  <div id="root">


    <router-view></router-view>
    <mainNavBar></mainNavBar>
  
  </div>


</template>

<script>
//  import { MessageBox } from 'mint-ui'
  import home from '../../home/home/home'
  import mine from '../../mine/mine/mine'
  import mainNavBar from '../mainNavBar'

  export default {
    name: 'root',
    data () {
      return {
        selected: '首页'
      }
    },
    components: {
      home,
      mine,
      mainNavBar
//      navbar
    },
    watch: {
      // 监听selected变量，当用户点击不同item的时候，将页面跳转至相应界面
      selected (newVal, oldVal) {
        if (newVal === '首页') {
          this.$router.replace({path: '/home'})
        } else if (newVal === '购物车') {
          this.$router.replace({path: '/ShoppingCart'})
        } else if (newVal === '我的') {
          this.$router.replace({path: '/Mine'})
        } else {
          this.$router.replace({path: '/home'})
        }
      }
    },
    destroyed () {
      console.log('root destroyed')
    },
    created () {
//      this.utils.alert(this.utils.isWXUA())
    },
    updated () {
      this.setTabbarItem()
    },
    mounted () {
      this.setTabbarItem()
    },
    methods: {
      setTabbarItem () {
        // 当通过/shoppingcart,/home,/mine进入的时候，将tabbar选择正确的item
        let path = this.$route.fullPath.toLocaleLowerCase()
        var name = ''
        if (path === '/home') {
          name = '首页'
        } else if (path === '/shoppingcart') {
          name = '购物车'
        } else if (path === '/mine') {
          name = '我的'
        } else if (path === '/') {
          name = '首页'
        } else {
          name = '首页'
        }
        if (name !== this.selected) {
          this.selected = name
        }
      }
    }
  }
</script>

<style lang="scss">
  
  $defaultColor: #191919;
  $baseFontSize: 750px;

  @function pxToRem($px) {
    @return $px / $baseFontSize * 10rem;
  }

  $defaultSize: pxToRem(24px);


  /*.mint-tabbar > .mint-tab-item.is-selected {*/
    /*background-color: rgba(255,0,0,0);*/
    /*color: #ff0000;*/
  /*}*/
  /*.mint-tabbar {*/
    /*height: pxToRem(110px);*/
  /*}*/
  #root {

  }

</style>



