<template>
	<!-- 配件购买 start-->
	<div class="grey-bg-sec">
		<div class="accessories-sec">
			<div class="part-header">
				<span>配件购买</span>
				<span class="icon-arrow-down" :class="{'icon-arrow-up':accesListShow==true}" @click="accesListShow=!accesListShow"></span>
			</div>
			<div class="part-list" v-show="accesListShow" >
				<div class="part-item" v-for="(item,index) in partList">
					<div class="part-img">
						<img :src="item.imgUrl" alt="">
					</div>
					<div class="part-operate">
						<div>
							<p>{{item.skuName}}</p>
							<p class="text-grey">¥{{item.price | filterPrice}}</p>
						</div>
						<span class="part-btn" @click="addPart(item.skuId,index)">添加</span>
					</div>
				</div>
			</div>
		</div>
	</div>
	<!-- 配件购买 end-->
</template>

<script>
	import '@/utils/getParts'
	import { mapState } from 'vuex'

	export default {
		data(){
			return {
				accesListShow: false,//显示配件
			}
		},
		computed: mapState({
			partList: state => state.shopCart.partList
		}),
		methods: {
			getPartList(){
				this.$store.dispatch('getPartList');
			},
			addPart(skuId,index){
				this.$store.dispatch('addPartToCart',{'sid': skuId,'index': index});
			}
		},
		mounted(){
			this.getPartList();
		}
	}


</script>


<style lang="scss">
$defaultColor: #191919;
$baseFontSize: 750px;

@function pxToRem($px) {
	@return $px / $baseFontSize * 10rem;
}

$defaultSize: pxToRem(24px);

.acces-border {
	border: 1px solid #BDBDBD;
}

.acces-table-footer {
	padding-right: pxToRem(40px);

	.acces-mark {
		border: none;
	}
}


.accessories-sec {
	margin-top: pxToRem(20px);
	width: 100%;
	background-color: #fff;
	color: #666;
	.part-header {
		padding: pxToRem(20px) pxToRem(40px) pxToRem(20px) pxToRem(60px);
		display: flex;
		justify-content: space-between;
		font-size: pxToRem(20px);
	}

	.part-list {
		padding: 0 pxToRem(50px) pxToRem(60px) pxToRem(50px);
		font-size: 0;

		.part-item {
			margin-top: pxToRem(35px);
			display: inline-block;
			width: pxToRem(312px);
			height: pxToRem(302px);
			border: 1px solid #ccc;
			
			&:nth-of-type(2n+1){
				margin-right: pxToRem(26px);
			}
			.part-img {
				margin: pxToRem(38px) auto pxToRem(30px);
				width: pxToRem(172px);
				height: pxToRem(172px);
				text-align: center;
				img {
					width: 100%;
					max-width: 100%;
				}
			}
		}
		.part-operate {
			padding: 0 pxToRem(20px);
			display: flex;
			justify-content: space-between;
			font-size: pxToRem(20px);

			.text-gray {
				font-size: pxToRem(18px);
			}

			.part-btn {
				display: inline-block;
				width: pxToRem(98px);
				height: pxToRem(42px);
				line-height: pxToRem(40px);
				text-align: center;
				font-size: pxToRem(24px);
				border: 1px solid #666;

			}
		}
	}
	
}


</style>


