
import Vue from 'vue';
import api from '@/api/api';


const CNAVSHOW = 'CNAVSHOW' ;//头部分类
const GETCLASSIFYPRO = 'GETCLASSIFYPRO';//分类页商品列表
const GETNAVLIST = 'GETNAVLIST';//分类list列表


const NAVSHOWTOGGLE = 'NAVSHOWTOGGLE';
const GETPROLIST = 'GETPROLIST'; 
const GETCATEGORYID = 'GETCATEGORYID';

export default {
	state: {
		navBtnShow: false,//是否显示头部导航按钮
		navShow: false,//右侧分类导航

		classifyProList: [],//分类页商品列表
		classifyPage: 0,
		classifyPageSize: 10,
		classifyTotalPage: 0,

		currCategoryId: null,
		currCategoryParentId: null,
	

		navList: {},//分类信息列表

	},
	actions: {
		classifyNavShow( {commit} ) {
			commit(CNAVSHOW);
		},
		getCategory( { commit,state },data){
			commit('GETCATEGORYID',{data: data});
		},
		getClassifyProList({dispatch, commit,state},data){ //分类页列表
		
			var parma = {
				categoryId: state.currCategoryId, //二级分类Id
				fristCategoryId: state.currCategoryParentId,//一级分类Id
				pageNo: state.classifyPage,
				pageSize: state.classifyPageSize
		    }
			console.log(parma);
			api.post('/getProList',parma,function(res){
				console.log(res.body);
				commit(GETCLASSIFYPRO,{
					data: res.body.data
				})
			})

		},
		getNavList({commit},data){
		 	api.get('/getClassfiyNav',{},function(res){
			 	commit( GETNAVLIST ,{
					data: res.body.data
				})
			})
		},
		navShowToggle({commit}){
			commit(NAVSHOWTOGGLE)
		},
	},
	mutations:{
		[CNAVSHOW](state){
			state.navBtnShow = !state.navBtnShow;
		},
		[GETCLASSIFYPRO](state,options){
			state.classifyProList = options.data.pageNo <= 1 ? options.data.itemList : state.classifyProList.concat(options.data.itemList);
			state.classifyPage = options.data.pageNo;
			state.classifyTotalPage = options.data.totalPage;
		},
		[GETNAVLIST](state,options){
			state.navList = options.data;
		},
		[NAVSHOWTOGGLE](state){
			state.navShow = !state.navShow;
		},
		[GETCATEGORYID](state,options){
			state.currCategoryId = options.data.id;
			state.currCategoryParentId = options.data.parentId;
			state.classifyPage = 0;
		}
	}
}


