import api from '../api'

export default {
  editUserInfo (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/editUserInfo', data, successCallback, errCallback, isShowErrMsg)
  },
  bindMobilePhone (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/userBindMobilePhone', data, successCallback, errCallback, isShowErrMsg)
  },//
  initSetPassword (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/initSetPassword', data, successCallback, errCallback, isShowErrMsg)
  },
  editPassword (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/initSetPassword', data, successCallback, errCallback, isShowErrMsg)
  }
}



