import api from '../api'

export default {
  login (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/user/login', data, successCallback, errCallback, isShowErrMsg)
  },
  userInfo (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/user/getUserInfo2', data, successCallback, errCallback, isShowErrMsg)
  },
  getHotCity (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/front/v1/baseinfo/city/queryHotCity', data, successCallback, errCallback, isShowErrMsg)
  },
  getToken (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/common/getToken', data, successCallback, errCallback, isShowErrMsg)
  },
  getAllCity (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/front/v1/baseinfo/city/searchAllCity', data, successCallback, errCallback, isShowErrMsg)
  },
  wxAutoSign (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/weixin/autoRegisterAndLogin', data, successCallback, errCallback, isShowErrMsg)
  },
  wxToken (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('https://api.weixin.qq.com/sns/oauth2/access_token', data, successCallback, errCallback, isShowErrMsg)
  },
  register (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/registerUser', data, successCallback, errCallback, isShowErrMsg)
  },
  login (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/userLogin', data, successCallback, errCallback, isShowErrMsg)
  },
  resetPassword (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/appclient/v1/user/resetPassword', data, successCallback, errCallback, isShowErrMsg)
  },
  getAuthCode (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/appclient/v1/verifycode/get', data, successCallback, errCallback, isShowErrMsg)
  },
  getUserInfo (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/appclient/v1/user/getUserInfo', data, successCallback, errCallback, isShowErrMsg)
  },
  getAllCity (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.get('api/common/v1/baseinfo/cityAll', data, successCallback, errCallback, isShowErrMsg)
  }
}



