<template>
	
<div class="default-panel">
	<div class="flex-bar">
		<span class="icon-goback" onClick= "history.back();"></span>
		<div class="flex-title">
			<span class="vt-middle">新增地址</span>
		</div>
		<span class="flex-right">
		</span>
	</div>
	<div class="grey-bg-sec">
		<div class="loginTips">
			<ul class="edit-list">
				<li>
					<div class="edit-item">
						<span>如此前已购买过，可直接登录</span>
						<router-link to="/login"><span class="icon-arrow-right"> </span></router-link>
					</div>
				</li>
			</ul>
		</div>
	</div>
	<div class="grey-bg-sec">
		<div class="loginTips">
			<ul class="edit-list">
				<li>
					<div class="edit-item">
						<span style="color: #666">配送方式</span>
						<span>配送上门</span>
					</div>
				</li>
			</ul>
		</div>
	</div>
	

	<div class="address-edit-sec">
		<ul class="edit-list">
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="name" placeholder="收货人">
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="phone" maxlength="11" 
					placeholder="收货人手机"
					/>
				</div>
			</li>
			<li>
				<div class="edit-item">
				<input type="text"  class="input" v-model="area" 
					placeholder="收货城市"
					readonly="readonly" 
					@click="popupVisible=true"
					/>
					<span class="icon-arrow-right"></span>
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text"  class="input" v-model="address" 
					placeholder="详细地址，如街道、楼层、门牌号等"
					/>
				</div>
			</li>
		</ul>

		<ul class="edit-list">
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="cusUser"  placeholder="下单人">
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="cusPhone" maxlength="11" 
					placeholder="下单人手机"
					/>
				</div>
			</li>
			<li>
				<div class="edit-item no-bor">
					<input type="text"  class="input code-input" v-model="code" 
					placeholder="短信验证码"
					/>
					<button class="code" type="button" @click="getVaildCode">{{codeText}}</button>
				</div>
			</li>
		</ul>
	</div>

	<button type="button" class="btn-save" @click="noLoginSave">确认</button>


	<mt-popup
 		v-model="popupVisible"
 		class="address-popup"
  		position="bottom">
		<mt-picker 
		:showToolbar= true
		:slots="slots" 
		@change="onValuesChange"
		>
			<span @click="popupVisible = false">取消</span><span @click="setAddressArea">确定</span>

		</mt-picker>
	</mt-popup>



</div>

</template>

<script>
	import Vue from 'vue';
	import { CellSwipe } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import { Cell } from 'mint-ui';
	import { Popup } from 'mint-ui';
	import { Picker } from 'mint-ui';
	import { Toast } from 'mint-ui';
	Vue.component(Picker.name, Picker);
	Vue.component(Popup.name, Popup);
	Vue.component(Cell.name, Cell);
	Vue.component(CellSwipe.name, CellSwipe);

	import normalBar from '@/components/common/navBar/normalBar'

	import '@/utils/addressEditGet'

	export default {
		data(){
			return {
				name: '',
				phone: '',
				area: '',
				address: '',

				cusUser:'',
				cusPhone:'',
				code: '',
				
				popupVisible: false,
				pickerArea: '',
				slots: [
			        {
			          flex: 1,
			          values: ['北京', '上海', '广东省', '湖北省', '湖南省', '广西省'],
			          className: 'slot1',
			          textAlign: 'right'
			        }, {
			          divider: true,
			          content: '-',
			          className: 'slot2'
			        }, {
			          flex: 1,
			          values: ['深圳', '广州', '惠洲'],
			          className: 'slot3',
			          textAlign: 'left'
			        }
			    ],
			    codeText: '获取验证码',
			    disabled: false
			}
		},
		components: {
			normalBar
		},
		watch: {
			phone(val){
				this.phone = val.replace(/\D/g,'');
			},
			cusPhone(val){
				this.cusPhone = val.replace(/\D/g,'');
			}
		},
		mounted(){

			
			
		},
		methods: {
			getVaildCode(){
				// this.codeText 
				let self = this;

				if(this.disabled == true) return false;


				let seconds= 60;
				self.disabled = true;
				let timer = setInterval(function(){
					seconds --;
					self.codeText = seconds +'s';

					if(seconds <=0){
						self.codeText = '获取验证码';
						self.disabled = false;
					}

				},1000)

			},
			noLoginSave(){
				// if(this.name == ''){
				// 	Toast('请填写收货人');
				// 	return false;
				// } else if(this.phone=='' || this.phone.length != 11){
				// 	Toast('请正确填写收货人手机');
				// 	return false;
				// }else if(this.area==''){
				// 	Toast('请选择城市');
				// 	return false;
				// }else if(this.address==''){
				// 	Toast('请填写详情地址');
				// 	return false;
				// }else if(this.cusUser==''){
				// 	Toast('请填写下单人');
				// 	return false;
				// }else if(this.cusPhone==''){
				// 	Toast('请填写下单人手机号');
				// 	return false;
				// }else if(this.code==''){
				// 	Toast('请填写验证码');
				// 	return false;
				// }


				this.$router.push({
					name: 'addressList',
					params: {
						isOrderAddress: this.$route.params.isOrderAddress
					}
				})

			},
			onValuesChange(picker, values) {
		      	console.log(picker.getSlotValue(3));
		      	console.log(values);

		      	var self  = this;
		      	if(values[0] == '北京'){
		      		picker.setSlotValues(1,['北京'])
		      	}else if(values[0] == '上海'){
		      		picker.setSlotValues(1,['上海'])
		      	}
		      	
		      	this.$nextTick(function(){
					this.pickerArea = values;
				})

    		},
    		setAddressArea(){
    			this.area = this.pickerArea;
    			this.popupVisible = false;
    		}
		}

	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	

	.address-edit-sec {
		width: 100%;
	}

	.loginTips .edit-list {
		.edit-item {
			padding: pxToRem(50px)  pxToRem(60px) pxToRem(50px) 0;
			border-bottom: none;
		}
	}

.edit-list {
	padding-left: pxToRem(50px);
	background-color: #fff;

	.edit-item {
		border-bottom: 1px solid #666666;
	}

	.edit-item  {
		display: flex;
		justify-content: space-between;
		padding: pxToRem(60px)  pxToRem(60px) pxToRem(30px) 0;
		&.no-bor {
			border: none;
		}

		.input {
			width: 100%;

			&.code-input {
				padding-bottom:  pxToRem(30px);
				width: 65%;
				border-bottom: 1px solid #666666;
			}

		}
		input::-webkit-input-placeholder {
			color: #BDBDBD;
		}
	}
}

.code {
	display: inline-block;
	border: 1px solid #666;
	color: #BDBDBD;
	background-color: #fff;
	width: pxToRem(180px);
	height: pxToRem(76px);

}

.btn-save {
	margin: pxToRem(100px) auto;
	display: block;
	width: 87%;
	height: pxToRem(100px);
	color: #fff;
	border: none;
	line-height: 1;
	font-size: pxToRem(28px);
	background-color: #D8331D;
}

.address-popup {
	width: 100%;
}
.picker-items {
	width: 100%;
}

.flex-bar {
	padding: 0 pxToRem(30px);
	display: flex;
	justify-content: space-around;
	height: pxToRem(100px);
	line-height: pxToRem(100px);
	border-bottom: 1px solid #eee;
}

.flex-title {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	background-color: rgba(255,255,255,0.90);
}
.edite {
	display: inline-block;
	width: pxToRem(60px);
	color: #666;
}
	
</style>


