<template>
  <div id="phoneNumlogin">

    <mt-header title="幸福西饼-登录" style="height: 50px">
      <router-link to="/" slot="left">
        <mt-button>
          <img src="../../../assets/icon/back.png" height="auto" width="8px" slot="icon" style="margin-left: 12px">
        </mt-button>
      </router-link>

      <router-link to="/register" slot="right">
        <mt-button  style="color: #666666">注册</mt-button>
      </router-link>

    </mt-header>
    <hr style="height: 1px;background-color: #d8d8d8;"/>

    <div class="phoneNumlogin-content">
      <div class="textfield-view">
        <input type="text"
               style="color:#191919"
               v-model="phonteNum"
               placeholder="手机号码"
               class="textfield"/>
        <hr style="height: 1px;background-color: #666666;"/>
      </div>

      <div class="textfield-view textfield-view2">
        <span class="textfield-view2-left">
          <input type="text"
                 style="color:#191919"
                 v-model="sms"
                 placeholder="短信验证码"
                 class="textfield"/>
          <hr style="height: 1px;background-color: #666666;"/>
        </span>
        <span v-bind:class="codeBtnClass" @click="getCode">
          {{codeBtnValue}}
        </span>


      </div>

      <div class="button" @click="login">
        登录
      </div>

      <div class="phoneNumlogin-footer">

        <router-link to="/login">
          <span class="go">账号登录</span>
        </router-link>

      </div>
    </div>


  </div>
</template>

<script>
  //  import { MessageBox } from 'mint-ui'
  import api from '../../../api/common/api.common'
  import cookieTools from '../../../js/cookie'
  export default {
    name: 'myBill',
    mounted () {
    },
    destroyed () {
    },
    computed: {
    },
    data () {
      return {
        codeBtnValue: '获取验证码',
        codeBtnClass: 'textfield-view2-right',
        phonteNum: '',
        sms: ''
      }
    },
    methods: {
      getCode (sender) {
        if (!this.utils.isMobileNum(this.phonteNum)) {
          this.utils.alert('请填写正确的手机号')
          return
        }

        var self = this
        var data = {}
        data.mobilePhone = this.phonteNum
        data.verifyCodeType = 2
        api.getAuthCode(data,function () {
          let sec =60;
          self.codeBtnClass = 'textfield-view2-right codeBtn-disabled'
          for(let  i=0; i<=60; i++){
            window.setTimeout(function(){
              if (sec != 0) {
                self.codeBtnValue =   sec + "秒后重发验证" ;
                sec--;
              } else {
                sec = 60;//如果倒计时结束就让  获取验证码显示出来
                self.codeBtnValue = '获取验证码'
                self.codeBtnClass = 'textfield-view2-right'
              }
            }, i * 1000)
          }
        })
      },
      login () {
        if (!this.utils.isMobileNum(this.phonteNum)) {
          this.utils.alert('请填写正确的手机号')
          return
        }

        if (this.sms.length > 6 || this.sms.length === 0) {
          this.utils.alert('验证码输入错误')
          return
        }

        var data = {}
        data.loginType = 1
        data.mobilePhone = this.phonteNum
        data.password = this.sms
        var self = this
        api.login(data,function (successRespond) {
          self.$store.dispatch('setLogin',true)
          localStorage.setItem('userIsLogin', true);
          localStorage.setItem('userloginTime', new Date());

          self.$router.push({path: '/'})
        })
      }
    }
  }
</script>

<style lang="scss">
  $baseFontSize: 750px;
  $defaultColor: #191919;
  @function pxToRem($px) {
    @return $px / $baseFontSize * 10rem;
  }
  $defaultSize: pxToRem(24px);

  .phoneNumlogin-content {
    margin-top: pxToRem(60px);
  }

  .textfield-view {

    margin-left: pxToRem(52px);
    margin-right: pxToRem(52px);
  }
  .textfield-view2 {
    display: flex;
    justify-content: space-between;
  }
  .textfield-view2-left {
    width: pxToRem(416px);
  }
  .textfield-view2-right {
    width: pxToRem(180px);
    text-align: center;
    line-height: pxToRem(76px);
    margin-top: pxToRem(43px);
    border:solid 1px  #191919;
  }


  .textfield {
    margin-top: pxToRem(60px);
    margin-bottom: pxToRem(30px);
  }
  .button {
    background: #3D2D23;
    height: pxToRem(100px);
    line-height: pxToRem(100px);
    text-align: center;
    color: white;
    margin-left: pxToRem(52px);
    margin-right: pxToRem(52px);
    margin-top: pxToRem(80px);
    font-size: pxToRem(28px);
  }
  .phoneNumlogin-footer {
    display: flex;
    justify-content: flex-end;
    margin-left: pxToRem(52px);
    margin-right: pxToRem(52px);
    margin-top: pxToRem(60px);
    color: #666666;
    font-size: pxToRem(24px);
  }

  .codeBtn-disabled {
    pointer-events: none;
    cursor: default;
    opacity: 0.6;
  }

  .go {
    color: #666666;
  }
</style>


