<template>
	<div>
		<normalBar title="确认订单"></normalBar>
		<div class="default-panel firm-panel">
			<div class="grey-bg-sec">
				<div class="address-sec" v-if="!isWxLogin&&!isSignSuccess">
					<span class="big-size">请新建收货地址h5游客</span>
					<span class="icon-arrow-right"  @click="goAddressList(0)"></span>
				</div>
				<div class="address-sec" v-if="!isSignSuccess && isWxLogin">
					<span class="big-size">请新建收货地址微信但未绑定手机号</span>
					<span class="icon-arrow-right"  @click="goAddressList(2)"></span>
				</div>
				
				<div class="address-sec" v-if=" isSignSuccess && addressCode == '03206' ">
					<span class="big-size">请新建收货地址已登录无地址</span>
					<span class="icon-arrow-right"  @click="goAddressList(1)"></span>
				</div>
				<div class="address-sec" v-if="isSignSuccess && addressCode == '03209'">
					<span class="big-size">无符合条件的配送地址，点击修改</span>
					<span class="icon-arrow-right"  @click="goAddressList(3)"></span>
				</div>
				<div class="address-sec" v-if="isSignSuccess && addressCode == '00000'">
					<div class="address-content">
						<p class="title">默认地址</p>
						<p class="big-size">{{orderAddress.recipientName}}<span class="last">收</span></p>
						<p>{{orderAddress.address}}</p>
						<p>{{orderAddress.recipientMobile}}</p>
					</div>
					<span class="icon-arrow-right" @click="goAddressList(1)"></span>
				</div>
			</div>

			<div class="grey-bg-sec">
				<div class="extra-sec">
					<div class="text-list">
						<mt-cell title="配送日期">
							<span class="text" v-on:click="openPicker">{{deliveryDate?deliveryDate:'未选择'}}</span>
							<span class="icon-arrow-right"></span>
						</mt-cell>
						<mt-cell title="配送时间">
							<span class="text" @click="timerShow = true">{{deliveryTime ? deliveryTime : '未选择'}}</span>
							<span class="icon-arrow-right"></span>
						</mt-cell>
						<mt-cell title="祝福贺卡">
							<input type="text"  v-model="greetingText" class="input-marks" placeholder="限定30字" maxlength="30" />
						</mt-cell>
						<mt-cell title="订单备注">
							<textarea v-model="orderMarks" class="input-marks orderMarks"  maxlength="60" placeholder="限定60字（请将购买需求在备注中详细说明）"/></textarea>
						</mt-cell>
					</div>	
				</div>
			</div>

			<div class="grey-bg-sec" v-for="(itemList,index) in preOrder">
				<div class="gift-sec">
					<div class="gift-header">·礼盒{{index== 0 ?'一': '二'}}·</div>
					<div class="gift-body">
						<div class="media" v-for="item in itemList.ItemVoList">
							<div class="media-left">
								<img :src="item.picture" alt="">
							</div>
							<div class="media-content">
								<p class="m-title">
									<span>{{item.displayName}}</span>
									<span class="m-small">X{{item.num}}</span>
								</p>
								<span class="m-subTitle text-grey">{{item.size}}</span>
								<div class="m-operate">
									<div class="m-operate-cont">
										<span class="price text-red">¥{{item.price | filterPrice}}</span>
										<span class="text-grey">
											生日牌：生日快乐
										</span>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>


		</div>

		
		<div class="firm-bottom-sec">
			<div class="firm-bottom-content">
				<div class="firm-bottom-left">
					<span>
						合计：<span class="text-red">¥{{finalOrderPrice | filterPrice}}</span>
					</span>
					<span class="text-grey">含运费</span>
				</div>
				<span class="checkout-btn" :class="{disabled: orderAddress.id ? '' : true}" @click="confirmOrder">
					提交订单
				</span>
			</div>
		</div>


		<mt-datetime-picker
		    ref="picker"
		    type="date"
		    v-model="pickerValue"
		    :visibleItemCount = 5
		    year-format="{value} 年"
			month-format="{value} 月"
			date-format="{value} 日"
			@confirm="confirmDate"
		>
		</mt-datetime-picker>


		
		<mt-popup
		  	v-model="timerShow"
		  	position="bottom"
			class='deliverPick'
		  	>
			<mt-picker 
					:slots="slots" 
					:showToolbar= true
					:visibleItemCount =3
					@change="onValuesChange">
					<span @click="timerShow = false">取消</span><span @click="setDeliveryTime">确定</span>
				</mt-picker>
		</mt-popup>

	</div>
</template>


<script>
	import Vue from 'vue';
	import { Picker } from 'mint-ui';
	import { Swipe, SwipeItem } from 'mint-ui';
	import { Cell } from 'mint-ui';
	import { DatetimePicker } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import { Popup } from 'mint-ui';
	import { Toast } from 'mint-ui';

	Vue.component(Popup.name, Popup);
	Vue.component(Picker.name, Picker);
	Vue.component(DatetimePicker.name, DatetimePicker);
	Vue.component(Swipe.name, Swipe);
	Vue.component(SwipeItem.name, SwipeItem);

	

	import normalBar from '@/components/common/navBar/normalBar'

	import '@/utils/firmOrderGet'
	import '@/utils/getDeliveryTime'
	import '@/utils/firmOrderConfirm'
	import '@/utils/firmOrderAddress'

	import { mapState } from 'vuex'
	export default{
		data(){
			return {
				addressValid: false,//地址正确
				pickerValue: '',
				deliveryDate: '',//配送日期
				deliveryTime: '',//配送时间
				tempTimer:'',
				timerShow: false,
				greetingText:'',
				orderMarks: '',
				slots: [
			        {
			          flex: 1,
			          values: [],
			          className: 'slot1',
			          textAlign: 'center',
			          showToolbar: true
			        }
			    ],
			    addressCode: '00000',//00000 正常， 03206 说明用户无收货地址 code 03209 说明没有匹配到当前区域的收货地址
			}
		},
		computed:{
			...mapState({
				preOrder: state=>state.shopCart.firmOrder.preOrderVoList,
				finalOrderPrice: state=>state.shopCart.firmOrder.finalOrderPrice,
				orderAddress: state=>state.shopCart.orderAddress,
				isSignSuccess: state=>state.user.isSignSuccess,
				isWxLogin: state=>state.user.isWxLogin,
				checkedAddress: state => state.addressState.checkedOrderAddress
			})

		},
		components: {
			normalBar
		},
		methods: {
			openPicker() {
        		this.$refs.picker.open();
      		},
      		openTimer(){
      			this.timerShow = true;
      		},
      		confirmDate(value){
      			let self = this;
      			let year = value.getFullYear() < 10 ? '0'+value.getFullYear() : value.getFullYear();
      			let month= (value.getMonth()+1) < 10 ? '0'+(value.getMonth()+1) : (value.getMonth()+1);
      			let days = value.getDate() < 10 ? '0' + value.getDate() : value.getDate();
      			this.deliveryDate =year+ '/'+month+'/'+days;
				

				let parma = {
					date: year+ '-'+month+'-'+days,
					regionalismId:440305
				}
				this.$store.dispatch('getDeliveryTimer',parma).then(function(res){
					console.log(res.times[0]);
					var times = res.times || [];
		            var timeFormat = function(time){
		              time = time.split(':');
		              var hour = time[0].length > 1 ? time[0] : ('0' + time[0]);
		              var minute = time[1].length > 1 ? time[1] : ('0' + time[1]);
		              return hour + ':' + minute;
		            };
		            var new_time_range = self.deliveryTimeRange().filter(function(n){
		              return times.some(function(t){
		                var a = n.split('~');
		                return a[0] >= timeFormat(t.startTime) && a[1] <= timeFormat(t.endTime);
		              });
		            });

					var new_time_range = self.deliveryTimeRange().filter(function(n){
		              return times.some(function(t){
		                var a = n.split('~');
		                return a[0] >= timeFormat(t.startTime) && a[1] <= timeFormat(t.endTime);
		              });
		            });
					self.slots[0].values = new_time_range;
				})
      		},
      		deliveryTimeRange(){
      			/**
		        '00:00~01:00',
		        '00:30~01:30',
		        ...
		        '22:30~23:30',
		        '23:00~24:00',
		        '23:30~24:30'
		       */
		      	var r = [];
		      	var format = function(time){ return time < 10 ? '0' + time : time; };
		      	for(var i=0; i<24; i++){
		        	r.push(
			          format(i) + ':00~' + format(i+1) + ':00',
			          format(i) + ':30~' + format(i+1) + ':30'
		        	)
		      	}
		      	return r;
		    },
      		setDeliveryTime(){//设置配送时间
				this.deliveryTime = this.tempTimer;
				this.timerShow = false;
      		},
      		onValuesChange(picker, values) {
			    this.tempTimer = values[0];
			},
			msgBox(){
				MessageBox.confirm('','',{
					title: '',
					message: '当前日期已暂停预约，请选择其他日期?',
					confirmButtonText: '选择日期'
				}).then(action => {
	 				this.openPicker();
				});
			},
			confirmOrder(){
				let self = this;
					
				if(self.recipientId == ''){
					Toast('请选择收货地址');
					return false;

				}else if(self.deliveryDate == ''){
					Toast('请选择配送日期');
					return false;

				}else if(self.deliveryTime == ''){
					Toast('请选择配送时间');
					return false;
					
				}else if(self.deliveryTime == ''){
					Toast('请选择配送时间');
					return false;
				}

				let formData = {
					deliveryTime: self.deliveryDate.replace(/\//g,'-')+' '+self.deliveryTime,//	配送时间	number	
					greetingCard: self.greetingText,//	祝福贺卡	number	
					recipientAddress: self.orderAddress.recipientAddress,//	收货地址	string	
					recipientId:   self.orderAddress.id,//	收货人id	number	
					recipientMobile: self.orderAddress.recipientMobile,//	收货人电话	string	
					regionalismId: self.orderAddress.regionalismId,//	区域id	number	
					remarks: self.orderMarks,//
				};


				console.log(formData);
				self.$store.dispatch('confirmOrder',formData).then(function(){
					self.$router.push({
						name: 'payTypes'
					})
				});
				
			},
			goAddressList(type){
				if(type == 0){
					this.$router.push({
						name: 'addressNoLoginAdd',
						query: {
							isOrderAddress: true
						}
					})

				}else if(type == 2){
					this.$router.push({
						path: '/bindPhone'
					})
				}else  if(type == 1){
					this.$router.push({
						name: 'addressList',
						query: {
							isOrderAddress: true
						}
					})
				}

				
			},
			getInit(){
				this.$store.dispatch('getFirmOrder');

				let self = this;

				let params = self.$route.query.address;

				if(self.isSignSuccess){
					if(!!!self.checkedAddress.id){
						self.$store.dispatch('getFirmOrderAddress')
						.then(function(code){
							self.addressCode = '00000';
						})
					}else {
						
						this.orderAddress = self.checkedAddress;
						console.log(params);
					}
				}


				
			}
		},
		mounted(){
			this.getInit();
		}
	}


</script>

<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);

	.firm-panel {

	}
	

	
	.address-sec {
		padding: pxToRem(60px) pxToRem(50px);
		display: flex;
		justify-content: space-between;
		font-size: pxToRem(22px);
		background-color: #fff;
		align-items: center;
		border-top: 1px solid #D4B675;

		.big-size {
			font-size: pxToRem(28px);
			.last {
				margin-left: pxToRem(10px);
			}
		}


		.address-content {
			line-height: 1.2em;
			p {
				margin-top: pxToRem(10px);
			}
			.title {
				font-size: pxToRem(20px);
				color: #666;
			}
		}



	}

	.extra-sec {
		background: #fff;
		.text-list {

			.text {
				color: #191919;
			}

			.mint-cell {
				padding-left: pxToRem(50px);
				min-height: pxToRem(120px);

				&:active {
					background: #fff;
				}

				&:not(:last-of-type){
					.mint-cell-wrapper {
						border-bottom: 1px solid #bdbdbd;
					}
				}
				
				.mint-cell-wrapper {
					padding-left: 0;
					padding-right: pxToRem(50px);
					
					.is-link {
						font-size: pxToRem(11px);
					}
				} 

				.mint-cell-text {
					color: #666;
				}
			}

			.icon-arrow-right {
				margin-left: pxToRem(20px);
			}
		}

		.input-marks {
			padding: pxToRem(5px) pxToRem(10px);
			width: pxToRem(440px);
			text-align: right;
			font-size: pxToRem(11px);
    		resize: none;
    		border: none;
			&::placeholder {
				color: #BDBDBD;
			}
		}
	}

	.gift-sec {
		background-color: #fff;
		.gift-header {
			text-align: center;
			line-height: pxToRem(66px);
			color: #030303;
		}

		.media{
			padding: pxToRem(34px) pxToRem(54px) pxToRem(34px) pxToRem(66px);
			border-top: 1px solid #eeeeee;

			.m-operate {
				padding-right: 0;
			}
		}
	}

	.firm-bottom-sec {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		font-size: $defaultSize;
		border-top: 1px solid #eee;
		background-color: #fff;

		.firm-bottom-content {
			display: flex;

			.firm-bottom-left {
				padding: 0 pxToRem(20px) 0 pxToRem(40px);
				flex: 1;
				display: inline-flex;
				align-items: center;
				justify-content: space-between;

				.text-gray {
					margin-left: pxToRem(20px);
				}

			}
			.checkout-btn {
				width: pxToRem(230px);
				height: pxToRem(115px);
				line-height: pxToRem(115px);
				text-align: center;
				color: #fff;
				font-size: pxToRem(32px);
				background-color: #D8331D;
				&.disabled {
					background-color: #CCCCCC;
				}
			}
		}
	}

	.mint-datetime-action {
		color: #544014;
		font-size: pxToRem(24px);
	}
	
	.deliverPick {
		width: 100%;
		
		
	}

	

</style>


