<template>
	
<div class="default-panel">
	<div class="flex-bar">
		<span class="icon-goback" onClick= "history.back();"></span>
		<div class="flex-title">
			<span class="vt-middle">{{isAdd ? '新增地址':'编辑地址'}}</span>
		</div>
		<span class="flex-right">
			<span class="edite" @click="addSave" v-if="isEdite && isAdd">保存</span>
			<span class="edite" @click="editSave" v-if="isEdite && !isAdd">保存</span>
			<span class="edite" @click="editeToggle" v-if="!isEdite && !isAdd">编辑</span>
		</span>
	</div>
	

	<div class="address-edit-sec">
		<ul class="edit-list">
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="name" v-bind:readonly="!isEdite" placeholder="收货人">
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text" class="input" v-model="phone" maxlength="11" v-bind:readonly="!isEdite"
					placeholder="收货人手机"
					/>
				</div>
			</li>
			<li>
				<div class="edit-item">
				<input type="text"  class="input" v-model="area" readonly="readonly"
					placeholder="收货城市"
					@click="isEdite ? popupVisible=true:''"
					/>
					<span class="icon-arrow-right"></span>
				</div>
			</li>
			<li>
				<div class="edit-item">
					<input type="text"  class="input" v-model="address" v-bind:readonly="!isEdite"
					placeholder="详细地址，如街道、楼层、门牌号等"
					/>
				</div>
			</li>
		</ul>
	</div>


	<mt-popup
 		v-model="popupVisible"
 		class="address-popup"
  		position="bottom">
		<mt-picker 
			:showToolbar= true
			:slots="slots" 
			@change="onValuesChange"
			valueKey = 'value'
		>
			<span @click="popupVisible = false">取消</span><span @click="setAddressArea">确定</span>

		</mt-picker>
	</mt-popup>



</div>

</template>

<script>
	import Vue from 'vue';
	import api from '@/api/api.js';
	import { CellSwipe } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import { Cell } from 'mint-ui';
	import { Popup } from 'mint-ui';
	import { Picker } from 'mint-ui';
	import { Toast } from 'mint-ui';
	Vue.component(Picker.name, Picker);
	Vue.component(Popup.name, Popup);
	Vue.component(Cell.name, Cell);
	Vue.component(CellSwipe.name, CellSwipe);

	import normalBar from '@/components/common/navBar/normalBar'

	import '@/utils/addressEditGet'
	import '@/utils/getRegion'
	import '@/utils/regionalismId'
	

	export default {
		data(){
			return {
				isAdd: true,//是否为添加
				name: '',
				phone: '',
				area: '',
				address: '',
				regionalismId:'',
				isEdite: true,
				
				popupVisible: false,
				pickerArea: '',
				slots: [
			        {
			          flex: 1,
			          values: [],
			          className: 'slot1',
			          textAlign: 'right'
			        }, {
			          divider: true,
			          content: '-',
			          className: 'slot2'
			        }, {
			          flex: 1,
			          values: [],
			          className: 'slot3',
			          textAlign: 'left'
			        }
			        , {
			          divider: true,
			          content: '-',
			          className: 'slot4'
			        }, {
			          flex: 1,
			          values: [],
			          className: 'slot5',
			          textAlign: 'left'
			        }
			    ],
			    curPro:'',
			    curCity: '',
			    regionalismId:''
			}
		},
		components: {
			normalBar
		},
		watch: {
			phone(val){
				this.phone = val.replace(/\D/g,'');
			},
			curPro(val,oldValue){
				let self = this;
				console.log(oldValue);
				this.$nextTick(function(){
		      		self.getProvince(oldValue).then(function(data){
						let regionArr  =[];
						for( let item in data){
							regionArr.push({
								key: item,
								value: data[item]
							})
						}
						self.slots[2].values = regionArr;
					});
				})

			},
			curCity(val,oldValue){
				let self = this;
				console.log(oldValue);
				this.$nextTick(function(){
		      		self.getRegional(oldValue).then(function(data){
						let regionArr  =[];
						for( let item in data){
							regionArr.push({
								key: item,
								value: data[item]
							})
						}
						self.slots[4].values = regionArr;
					});
				})
				
			}

		},
		mounted(){
			let self = this;
			let editId = this.$route.params.id;
				
			if(!!editId){
				this.$store.dispatch('getEditAddress',editId).then(function(res){
					console.log(123);
					self.name = '河马';
					self.phone =  '15666512453';
					self.area =  '广东省 深圳市 南山区';
					self.address =  '习大大总书记居住详细地址';



					self.isEdite = false;
					self.isAdd = false;
				})
				
			}else {
				this.isEdite = true;
				this.isAdd = true;
			}

			this.getProvince().then(function(data){
					let regionArr  =[];
					for( let item in data){
						regionArr.push({
							key: item,
							value: data[item]
						})
					}
					self.slots[0].values = regionArr;
			});
		},
		methods: {
			setAddressArea(){
    			this.area = this.pickerArea;
    			this.popupVisible = false;
    		},
			editeToggle(){
				if(this.isAdd){
					this.validAddress();
				}else {
					if(this.isEdite){
						
						this.validAddress()
						// this.$router.push({name: 'addressList'})
					}else {
						this.isEdite = !this.isEdite;
					}
				}
			},
			validAddress(routerName){
				if(this.name == ''){
					Toast('请填写收货人');
					return false;
				} else if(this.phone=='' || this.phone.length != 11){
					Toast('请正确填写收货人手机');
					return false;
				}else if(this.area==''){
					Toast('请选择城市');
					return false;
				}else if(this.address==''){
					Toast('请填写详情地址');
					return false;
				}
				let params = {
					address: this.address,//	地址	number	
					recipientMobile: this.phone,//	收货人手机号	number	
					recipientName: this.name,//	收货人名字	number	
					regionalismId: this.regionalismId//
				};
				this.$router.push({name: 'addressList'})
			},
			addSave(){
				this.validAddress();
			},
			editSave(){
				this.validAddress();
			},
			onValuesChange(picker, values) {
		      	var self  = this;
		      
				console.log(values);

				this.$nextTick(function(){
					self.curPro = values[0] ?  values[0].key : '';
					self.curCity = values[1] ?  values[1].key : '';
					self.regionalismId = values[2] ?  values[2].key : '';
					self.pickerArea = values[2] ? values.map(function(item){
						return item.value;
					}):'';

				})
				

				// if(!!this.curPro) return false;
		      	
		  //     	this.$nextTick(function(){
		  //     		self.getProvince().then(function(data){
				// 		let regionArr  =[];
				// 		for( let item in data){
				// 			regionArr.push({
				// 				key: item,
				// 				value: data[item]
				// 			})
				// 		}
				// 		self.regionList = regionArr;

				// 		// picker.setSlotValues(0, regionArr);
				// 	});

				// 	self.pickerArea = values;
				// })

    		},
    		getProvince(id){//省市
    			let self = this;
    			id = id ? id: 0;
    			return new Promise(function(reslove,reject){
    				api.get('/api/common/v1/baseinfo/region',{regionalismId:id},function(res){

						reslove(res.body.data);

					})
    			})
				
    		},
    		getRegional(id){
    			return new Promise(function(reslove,reject){
    				api.get('/api/common/v1/baseinfo/region/'+id,{},function(res){

						reslove(res.body.data);

					})
    			})
    		}
    
		}
	}
</script>
<style lang="scss">
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);
	

	.address-edit-sec {
		width: 100%;
	}

	.loginTips .edit-list {
		.edit-item {
			padding: pxToRem(50px)  pxToRem(60px) pxToRem(50px) 0;
			border-bottom: none;
		}
	}

.edit-list {
	padding-left: pxToRem(50px);
	background-color: #fff;

	.edit-item {
		border-bottom: 1px solid #666666;
	}

	.edit-item  {
		display: flex;
		justify-content: space-between;
		padding: pxToRem(60px)  pxToRem(60px) pxToRem(30px) 0;
		&.no-bor {
			border: none;
		}

		.input {
			width: 100%;

			&.code-input {
				padding-bottom:  pxToRem(30px);
				width: 65%;
				border-bottom: 1px solid #666666;
			}

		}
		input::-webkit-input-placeholder {
			color: #BDBDBD;
		}
	}
}

.code {
	display: inline-block;
	border: 1px solid #666;
	color: #BDBDBD;
	background-color: #fff;
	width: pxToRem(180px);
	height: pxToRem(76px);

}

.btn-save {
	margin: pxToRem(100px) auto;
	display: block;
	width: 87%;
	height: pxToRem(100px);
	color: #fff;
	border: none;
	line-height: 1;
	font-size: pxToRem(28px);
	background-color: #D8331D;
}

.address-popup {
	width: 100%;
}
.picker-items {
	width: 100%;
}

.flex-bar {
	padding: 0 pxToRem(30px);
	display: flex;
	justify-content: space-around;
	height: pxToRem(100px);
	line-height: pxToRem(100px);
	border-bottom: 1px solid #eee;
}

.flex-title {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	background-color: rgba(255,255,255,0.90);
}
.edite {
	display: inline-block;
	width: pxToRem(60px);
	color: #666;
}
	
</style>


